import {SphereGeometry, ConeGeometry, CylinderGeometry, Group, Euler, Color, MeshBasicMaterial, Mesh} from "three";
import Util from "../std/Util.js";
import Constants from "../std/Constants.js";
import Dump from "../std/Dump.js";

// Create a rocket icon.
//   height: the size of the rocket (in animation units)
//   name:   a name for the rocket.
class Rocket extends Group {
    constructor(args) {
        
        // Group takes no arguments in constructor
        super();
    
        let height = 1;
        if (args.height != null) {
            height = args.height;
        }
    
        if (args.name == null) {
            this.name = "Rocket";
        } else {
            this.name = args.name;
        }
        const Gnosecone = new ConeGeometry(0.15*height, 0.3*height, 8);
        const Gbody     = new CylinderGeometry(0.15*height, 0.2*height, 0.6*height, 8);
        const Gring     = new CylinderGeometry(0.25*height, 0.25*height, 0.1*height, 8);
        const Gfire     = new ConeGeometry(0.15*height, 0.3*height, 8);
        const sm1       = new MeshBasicMaterial({wireframe: false, color: new Color(0x44DD88)});
        const sm2       = new MeshBasicMaterial({wireframe: false, color: new Color(0x6666dd)});
        const sm3       = new MeshBasicMaterial({wireframe: false, color: new Color(0xdd8888)});
        const sm4       = new MeshBasicMaterial({wireframe: false, color: new Color(0xff0000)});
        const Mnosecone = new Mesh(Gnosecone, sm1);
    
        Mnosecone.rotateX(Math.PI/2);
        const Mbody     = new Mesh(Gbody, sm2);
        Mbody.rotateX(Math.PI/2);
        const Mring     = new Mesh(Gring, sm3);
        Mring.rotateX(Math.PI/2);
    
        Mnosecone.position.set(0, 0, -0.15*height);
        Mbody.position.set    (0, 0, -0.60*height);
        Mring.position.set    (0, 0, -0.95*height);
    
        
        this.add(Mnosecone);
        this.add(Mbody);
        this.add(Mring);
    
        // Add in a 'fire' component to show engines thrusting.
        // Visible only when 
        let Mfire = new Mesh(Gfire, sm4);
        Mfire.rotateX(Math.PI/2);
        Mfire.position.set(0, 0, -1.18*height);    
        this.add(Mfire);        
        this.Mfire     = Mfire;
        this.Mfire.visible = false;
        this.meta = {type:"Rocket", name: this.name};
        Mnosecone.meta = this.meta;
        Mbody.meta = this.meta;
        Mring.meta = this.meta;
        Mfire.meta = this.meta;
    }    
}
export default Rocket;

