import {Color, BufferGeometry, PointsMaterial, Float32BufferAttribute, Vector3} from "three";
import AngPoints from "../three/AngPoints.js";
import Util from "../std/Util.js";
import Constants from "../std/Constants.js";

const Satellites = function(satData, startTime) {
    function getPositions(data, time) {
        
        const px = [];
        for (var i=0; i<data.length; i += 1) {
            
            if (data[i].orbit == null) {
                Util.log("No orbit for "+i+" "+data.name);
            }
            const p = data[i].orbit.position(time);
            px.push(p[0], p[1], p[2]);
        }
        return new Float32BufferAttribute(px,3);
    }
    

    const geometry = new BufferGeometry();        
    // Should the time here be 0?
    geometry.setAttribute( 'position', getPositions(satData, startTime));
    geometry.setAttribute( 'color',    getColors(satData));
        
    const material       = new PointsMaterial( {sizeAttenuation: false, size: 2,  vertexColors: true} );
    const points         = new AngPoints(geometry, material );
    
    // Add this function to the component.  This allows us to find
    // a specific satellite.
    points.getIndexedMeta = function(index) {
        let meta = {...satData[index], type: "satellite"};
        return meta;
    };
    let currYear  = 3000;
    let here      = this;
    
    return {component: points,
      render:function(time) {
        let yr = Math.floor(2000 + 100*time/Constants.CENTURY);
        if (yr != currYear) {
            console.log("Updateing colors of satellites");
            geometry.setAttribute( 'color',  getColors(satData,yr));
            currYear = yr;            
        }
        geometry.setAttribute( 'position', getPositions(satData, time));
      }
    };
    
    
    function getColors(data, yr) {
        
        if (yr == null) {
            yr = 3000;
        }
        const colors = [];
        let bcount = 0;
        let lastColor = 0;
        for (var i=0; i<data.length; i += 1) {
            var sat = data[i];
            var sn = sat.name.toUpperCase();
            // Satellites not yet launched are black.
            if (sat.lyr > yr) {
                colors.push(0,0,0);
                bcount += 1;
            } else {
                lastColor = i;
                if (sat.name.indexOf("DEB") > 0) {
                    colors.push(1,0,0);
                } else if (sat.name.startsWith("TBA_")) {
                    colors.push(1,1,0);
                } else if (sn.indexOf("STARLINK") >= 0) {
                    colors.push(0,1,0);
                } else {
                    colors.push(1,1,1);
                }
            }
            geometry.setDrawRange(0,lastColor+1);
        }
        return new Float32BufferAttribute(colors, 3 );
    }
}

export default Satellites;