// This is the parent class of the Actions taken in response to scripted commands.
// Note the distinction between the Command, which is the request to do something
// and the Action which carries it out.  In some cases context for the action may
// be stored in the associated Command.

import Acts from "./Acts.js";

class Action {
    
    constructor(ptr, anim, traj) {
        this.ptr  = ptr;
        this.anim = anim;
        this.traj = traj;
        this.time = ptr.elem.t;

    }
    
    parseArgs() {
        let args = this.ptr.elem.forward;
        if (args == null) {
            args = "";
        }
        args = args.trim();        
        let fields = args.split(/\s+/);
        let keyvals = {};
        for (let i=0; i<fields.length; i += 1) {
            let str = fields[i];
            let pos = str.indexOf("=");
            if (pos > 0) {
                let key = str.substring(0,pos);
                let val = str.substring(pos+1);
                keyvals[key] = val;
            }
        }
        return {fields:fields, keyvals:keyvals};
    }
    
    execute() {
        console.log("No commmand action specified");
    }
    
    // Every instantiating subclass of Action will define a method
    //   execute(force)
    // which actually performs the request.  The force argument is
    // true when a request is being forced by another request that is
    // being requested going backwards.

    // Find and execute command from earlier in the stack.  Normally
    // used when anim.delta < 0.
    executePrev(ptr, cmds) {
        let prev = this.findPrev(ptr, cmds);
        if (prev == null) {
            return false;
        } else {
            this.runPrev(prev);
            return true;
        }
    }
    
    // Find the most recent previous command of the specified types.
    findPrev(ptr, cmds) {
        let curr = ptr.prev;
        while (true) {
            let cmd = curr.elem.command;
            if (cmd == "START") {
                return null;
            }
            let zz = ["a", "b", "c"];
            if (cmds.includes(cmd)) {
                return curr;
            }
            curr = curr.prev;
        }
    }
    
    // Run a previous command.
    runPrev(prev) {
         // This has to be in cache since we must have seen this command before.
        let Aclass = Acts[prev.elem.command];
        let prevAct = new Aclass(prev, this.anim, this.traj);
        // Do it now, not at the time the other command is executed going forward.
        prevAct.time = this.time;
        prevAct.execute(true);
    }
    
    // Remove all events subsequent to the current event that are generated by
    // the current event.
    cleanGenerated() {
        let curr = this.ptr.next;
        let cmp  = this.ptr.elem;
        while (curr.elem.command != "END") {
            
            let gen = curr.elem.generated;
            
            if (gen && (gen == cmp) ) {
                this.traj.sequence.del(curr);
                curr = curr.next;
            } else {
                curr = curr.next;
            }
        }
    }
    
    parseDuration(d) {
        d = d.toLowerCase().trim();
        let t = parseFloat(d);
        if (d.endsWith("d")) {
            t *= 86400;
        } else if (d.endsWith("y")) {
            t *= 365.25*86400;
        }
        return t;
    }    
}

export default Action;

