import Util from "../std/Util.js";

// Allow the user to add and delete forces
class CompoundForce {
    
    forces   = {};
    thrusts  = {};
    lastThrust;
    trajectory = null;
    
    add(name, force, isThrust) {
        this.forces[name] = force;
        if (isThrust) {
            this.thrusts[name] = true;
        }        
    }
    
    del(name) {
        delete(this.forces[name]);
        delete(this.thrusts[name]);
    }
    
    setDoThrust(value) {
        this.doThrust = value;
    }
    
    getLastThrust() {
        return this.lastThrust;
    }
    
    
    // Sum the forces.
    a(x,v,t) {        
        let res         = [0,0,0];
        this.lastThrust = [0,0,0];
        for (let name in this.forces) {
            let acc = this.forces[name].a(x,v,t);
            res = Util.vadd(res, acc);
            // Add up only thrusts too.
            if (this.thrusts[name]) {
                this.lastThrust = Util.vadd(this.lastThrust, acc);
            }
        }
        return res;
    }
}

export default CompoundForce;

