import {Component, createRef} from "react";
import Util from "../std/Util.js";
/** This class is used to create text boxes.
 *  If the text box is only going to be used when the user finishes the entry
 *  (by typing ENTER or CR), then generally only the 'callback' property should be provided.
 *  If the value of the text box may need to be inferred after an action on
 *  some other element, then the 'update' callback should be used to make sure
 *  to update the property value as the user enters data (since the user may
 *  not terminate their data entry with a CR).  
 *  The 'mountCallback' may be used if the field is to be manipulated at initialization
 *  and so we wait until it is mounted and then call this routine once.
 */
class TextInput extends Component {
    constructor(props) {
        super(props);
        
        if (props.mountCallback) {
            this.mountCallback = props.mountCallback;
        }
        
        if (props.placeholder) {
            this.placeholder = props.placeholder;            
        } else {
            this.placeholder = "Input...";
        }
        this.self = createRef();
        
        this.round = props.round;
        if (props.id) {
            this.id = props.id;
        } else {
            TextInput.idCount += 1;
            this.id = "ti"+TextInput.idCount;
        }
                
        if (props.callback) {
            this.callback = props.callback;
        } else {
            this.callback = null;
        }
        
        if (props.update) {
            this.update = props.update;
        }
        
        if (props.size) {
            this.size = props.size;
        } else {
            this.size = 16;
        }
        this.fieldID = props.fieldID;
                
        this.dft = props.defaultValue;
        
        this.state = {value: this.dft};
    }
    static idCount=0;
        
    render() {
        let here = this;
        let val  = this.state.value;
        if (val && this.round) {
            if (typeof val == "string") {
                val = Number(val);
            }
            val = val.toFixed(this.round);
        }
        
        return (
            <input ref={this.self} size={here.size} 
                   defaultValue={val}                   
                   placeholder={here.placeholder}
                   id={this.id}
                   onChange={                   
                        (event) => {
                          if (here.update) {
                            here.update(event.target.value);
                          }
                        }
                   }
                   onKeyUp={
                        (event) => {
                          if (event.key === 'Enter'  && here.callback) {
                            here.callback(event.target.value);
                          }
                        }
                    }
            />
        )
    }
    
    getValue(val) {
        return this.state.value;
    }
    
    setValue(val) {
        this.self.current.value = val;
    }
    
}

export default TextInput;