import {Component} from "react";
import Units from "./Units.js";
import ParameterFactory from "../orbits/ParameterFactory.js";
import Parameter from "../orbits/Parameter.js";

import Util from "../std/Util.js";
import Dump from "../std/Dump.js";

class Row extends Component {
    constructor(args) {
        
        super(args);
        this.param       = args.param;   // Parameter info
        this.name        = args.param.name;
        // Note that the value is the underlying value of the parameter
        // This is different
        this.calc        = args.calc;
        this.rowState    = null;
    }
    
    // This is called every time the value changes.
    handleChange(event) {
        let value = event.target.value;
        let oldState = this.calc.state[this.name];
        if (value == this.calc.state.entry) {
            return;  // Just ignore events that don't change value.
        }
        if (oldState.computed) {
            this.calc.status("Attempt to change computed value.  Clear before update.");
            return;
        }
        this.calc.updateRow(this.name, {entry: value, value: null, computed: false, 
                     validated:false, unitIndex: -1, initIndex: -1});
        this.calc.status("");
    }
    
    keyUp(event) {
        if (event.key === 'Enter') {            
            this.validate(event);
        }
    }
    
    validate(event) {
        let oldState = this.calc.state[this.name];
        if (oldState.validated) {
            return;
        }
        if (!event.target.value) {
            // Called in empty row to see if row is computable.
            this.calc.tryRow(this.param);
            return;
        }
        let resCh = this.calc.rowLegal(this.param, oldState);
        if (resCh.status == "validated") {
            
            this.calc.updateRow(this.param.name, resCh.rowState);
        }
        if (resCh.status != "error") {
            this.calc.status("");
        }
    }
                    
    renderNow() {
        let unit = null;
        if (this.rowState.unitIndex >= 0) {
            unit = this.param.units[this.rowState.unitIndex];
        }
        let val = this.renderToUnit(unit);
        if (val == null) {
            val = "";
        }
        return val;
    }
    
    renderToUnit(unit) {
        if (!this.rowState.validated) {
            return this.rowState.entry;
        }
        // Return user entry if in same units as originally.
        if (!this.rowState.computed && (this.rowState.unitIndex == this.rowState.initUnit)) {
            return this.rowState.entry;
        }
        let scale = 1;
        let offset = 0;
        if (unit) {
            scale  = unit.scale;
            offset = unit.offset? unit.offset: 0;
        }
        let scPrec = 7;
        let vcPrec = 5;
        if (this.calc.state.highPrec) {
            scPrec = 14;
            vcPrec = 12;
        }
        
        let suffix = "";
        if (unit) {
            suffix = " "+unit.unit;
        }
        if (this.param.dim == Parameter.SCALAR) {
            return this.dispScalar(this.rowState.value, scPrec, scale, offset)+suffix;
        } else {
            const vec = this.rowState.value;
            return "[ "+this.dispVector(vec, vcPrec, scale, offset)+" ]"+suffix;
        }
    }
    
    dispScalar(val, prec, scale, offset) {
        return (val/scale+offset).toPrecision(prec);
    }
        
    dispVector(vec, prec, scale, offset) {
        var str = "";
        var prefix = "";
        for (var i=0; i<vec.length; i += 1) {
            str += prefix + this.dispScalar(vec[i], prec, scale, offset);
            prefix = " ";
        }
        return str;
    }
    
    render() {
        const here    = this;
        this.rowState = this.calc.state[this.name];
        const cname   = this.rowState.computed? "yellow": "white";
        let sep = "";
        let value = this.renderNow();
        
        if (this.param.name == "m1" && this.value) {
            sep = <select id="frame"><option>CoM</option><option>Sep</option></select>;``
        }
        return (
                <tr key={this.name} className={cname} >
                    <th align="right">{this.name}</th>
                    <td><input size="18"
                          value={value}
                          onChange={this.handleChange.bind(this)}
                          onKeyUp={this.keyUp.bind(this)}
                       />
                    </td>
                    <td><Units calc={here.calc} name={here.param.name} units={here.param.units} /></td>
                    <td>{sep}{this.param.descrip}</td>
                </tr>
        );
    }
}

export default Row;
