import Action    from "./Action.js";
import Command   from "../Command.js";
import Trans     from "../../traj/Trans.js";
import Util      from "../../std/Util.js"; 
import Constants from "../../std/Constants.js"; 

class transfer extends Action {
    constructor(ptr, anim, traj) {
        super(ptr, anim, traj);
    }
    
    // Transfer from the current orbit to the requested orbit.
    // In terms of actions this simply add generated commands to the queue.
    execute(reverse) {
        if (this.anim.delta > 0) {
            this.cleanGenerated();
            this.initiate();
        }
    }
    
    initiate() {
        // Current handle two tokens: destination and time of transfer
        let fields = this.ptr.elem.forward.split(" ");
        let dest   = fields[0];
        let durat  = this.parseDuration(fields[1]);
        if (isNaN(durat)  || durat <= 0) {
            this.anim.setQuickHTML("Illegal transfer duration: ", fields[1]);
        }
        let p0     = this.traj.lastState;
        let seq    = this.traj.sequence;
        let here   = this;
        function startTransfer(bodies) {
            if (!bodies || !bodies[0] || !bodies[0].meta || !bodies[0].meta.orbit) {
                here.anim.setQuickHTML("Unable to find destination: "+dest);
                return;
            }
            let orb   = bodies[0].meta.orbit;
            let p1    = orb.stateVector(here.time + durat);
            let tr    = new Trans(p0[0], p1[0]);
            let trOrb = tr.orbit(here.time, durat);
            let s0    = trOrb.stateVector(here.time);
            let s1    = trOrb.stateVector(here.time+durat);
            let dx0   = Util.mag(Util.diff(p0[0],s0[0]))/Constants.AU;
            let dx1   = Util.mag(Util.diff(p1[0],s1[0]))/Constants.AU;
            
            // Calculate the change in velocities that we use to compute the impulses.
            let dv0   = Util.diff(s0[1], p0[1]);
            let dv1   = Util.diff(p1[1], s1[1]);
            let imp0  = new Command(here.time, "impulse", dv0[0]+" "+dv0[1]+" "+dv0[2]);
            let imp1  = new Command(here.time+durat, "impulse", dv1[0]+" "+dv1[1]+" "+dv1[2]);
            imp0.setGenerated(here);
            imp1.setGenerated(here);
            seq.add(imp0);
            seq.add(imp1);                        
        }
        this.anim.find(dest.trim(), startTransfer);
    }
}

export default transfer;
