import Gravity       from "../math/Gravity.js";
import CompoundForce from "../math/CompoundForce.js";
import Constants     from "../std/Constants.js";
import Gradient      from "../math/Gradient.js";

class ThrustByGradient extends CompoundForce {
    
    constructor(thrust, grad) {
        super();
        this.add("Grav", new Gravity(Constants.MSun),  false);
        this.add("Thrust", new Gradient(thrust, grad),     true);
    }    
}
export default ThrustByGradient;