import {Component} from 'react';
import Constants from "../std/Constants.js";
import Util from "../std/Util.js";
import TextInput from "../elems/TextInput.js";
import TrajControl from "../elems/TrajControl";
import ObjList from "../elems/ObjList.js";

class OptionsSelector extends Component {
    constructor(props) {
        super(props);
        this.anim   = props.anim;
        this.holder = props.holder;  
        this.state  = {controlClick:this.anim.frame.controlClick};
    }
    static index = 0;
    setAnimation(anim) {
        this.anim = anim;
    }
    
    switchClick() {
        this.anim.frame.controlClick = !this.state.controlClick;
        this.setState({controlClick: !this.state.controlClick});
    }
    
    componentDidMount() {
        this.holder.options = this;
    }
    
    render() {
        let clickText = "";
        if (!this.state.controlClick) {
            clickText = "Searching on any click";
        } else {
            clickText = "Searching only on control click";
        }
        return (
             <div>
             <ul>
                 <li> Set time <TextInput callback={this.setTime.bind(this)} placeholder="  time "/></li>
                 <li> Find <TextInput callback={this.object.bind(this)} placeholder="  object  "/> </li>
                 <li onClick={this.switchAnimation.bind(this)}> Select animation </li>
                 <li onClick={this.selectCapability.bind(this)}> Orbits and trajectories </li>
                 <li onClick={this.switchClick.bind(this)}>{clickText}</li>
                 <li onClick={this.openHelp.bind(this)}> Help </li>
              </ul>
             </div>
        );
    }

    openHelp() {
        window.open(this.anim.getHelpURL(), "help");
    }
    
    switchAnimation() {
        let choice=
          <div> Primary animations include
            <ul>
            <li onClick={this.solarSystem.bind(this)}>Solar System (includes many subanimations)</li>
            <li onClick={this.earth.bind(this)}>Earth</li>
            <li onClick={this.lgn.bind(this)}>Local Galactic Neighborhood</li>
            <li onClick={this.galax.bind(this)}>Nearby Galaxies</li>
            <li onClick={this.clusters.bind(this)}>Clusters of Galaxies</li>
            </ul>
          </div>
          ;
//            <li onClick={this.testAnim.bind(this)}>TestAnimation</li>
        this.anim.setQuickHTML(null);
        this.holder.setState({mode: "content", content: choice});
    }

    testAnim() {
        this.anim.setQuickHTML(null);
        this.setAnim("TestAnimation");
    }
    earth() {
        this.anim.setQuickHTML(null);
        this.setAnim("Earth");
    }
    solarSystem() {
        this.anim.setQuickHTML(null);
        this.setAnim("Solar System");
    }
    lgn() {
        this.anim.setQuickHTML(null);
        this.setAnim("Local Galactic Neighborhood");
    }
    galax() {
        this.anim.setQuickHTML(null);
        this.setAnim("Nearby Galaxies");
    }
    clusters() {
        this.anim.setQuickHTML(null);
        this.setAnim("Abell Clusters");
    }
    
    setAnim(name) {
        this.anim.setQuickHTML(null);
        this.anim.setAnim(name);
    }

    setTime(time) {
        // We will support 3 formats for time.
        //   ISO formats (supported by JavaScript parse function)
        //   yyyy[.ffff] (year and fraction of year)
        //   Julian days

        if (!time || time.trim().length == 0) {
            return;
        }
        let aTime = Util.parseTime(time);
        if (aTime != null  && !isNaN(aTime)) {
            this.anim.setTime(aTime);
        }
    }

    object(value) {
        this.anim.setQuickHTML(null);
        this.anim.find(value, this.found.bind(this));
    }

    found(list) {
        if (list != null && list.length > 0) {
            this.anim.setQuickHTML(null);
            // All of the actions associated with objects
            // are included in other classes.
            this.holder.setState({
                mode: "content",
                content: <ObjList holder={this.holder} list={list} anim={this.anim} />});
        } else {
            this.anim.setQuickHTML("No matches found");
        }
    }
    
    selectCapability() {
        let verb = "Add";
        if (this.anim.frame.trajControl) {
            verb = "Control";
        }
        let choice = 
          <div>
            <ul>
            <li onClick={this.orbParam.bind(this)}>Define orbit parameters</li>
            <li onClick={this.trajectory.bind(this)}> {verb} trajectory</li>
            </ul>
          </div>;
        this.holder.setState({mode: "content", content: choice});
    }

    orbParam() {
        // Open the orbit params window.  Note that since
        // this window shares code with the animation window,
        // we actually call the same base URL and distinguish
        // the window with a CGI parameer.
        window.open("?app=OrbParams", "params");
    }
    
    trajectory() {
        this.anim.setQuickHTML(null);
        let initStage = "init";
        if (this.anim.frame.trajControl) {
            initStage = "run";
        }
        let content = <TrajControl anim={this.anim} initStage={initStage} />;
        this.holder.setState({mode: "content", content:content});
    }
}

export default OptionsSelector;