import Constants from "../std/Constants.js";
import Util from "../std/Util.js";
import Dump from "../std/Dump.js";
class Gravity {
    
    /** Create a gravitational attractor
     *    M:   Mass of the attractor (kg)
     *    pos: The position of the attractor.  Defaults to origin, but
     *         may be the position function for an orbit or
     *         some other constant vector.
     */
    constructor(M, pos) {
        this.GM = Constants.G*M;
        if (pos == null) { // Central attractor
            this.pos = function() {return [0,0,0];};
        } else if (typeof(pos) != "function") {
            // Fixed point attractor (presumably not at origin)
            this.pos = function() {return pos};            
        } else {
            // Moving attractor
            this.pos = pos;
        }
    }
    
    a(x,v,t) {
        
        let p0 = this.pos(t);
        
        let delta = Util.diff(p0,x);
        let mag   = Util.mag(delta);
        
        let g = Util.times((this.GM)/(mag*mag*mag), delta);
        return g;
    }
}

export default Gravity;