import Animation from "../elems/Animation.js";
import Util from "../std/Util.js";
import Dump from "../std/Dump.js";

import {Scene, Vector3, Euler, Color,
        SphereGeometry, Mesh, MeshBasicMaterial, DirectionalLight, AmbientLight} from "three";

class Transition extends Animation {
    constructor(params) {
        super(params);
        
        
        this.cameraPos = new Vector3(0,0,6);
        this.cameraRot = new Euler(0,0,0,"XYZ");
        let steps = this.minSteps || 100;
        
        let sign = params.sign || -1;
        let scale = 1.01;
        let scale2 = 0.51;
        if (sign < 0) {
            sign = -1;
        } else {
            sign  = 1;
            scale = 0.01;
        }
        
        const here = this; 
        
        this.scene       = new Scene();
        this.textShowing = false;
        let count = 0;
        this.time = 0;
        this.delta = 0.001;
        let factor = 4;
        let colors = [0xdd0000, 0xdd8800, 0xdddd00, 0x88dd00, 0x00dd00, 0x00dd88, 
                      0x00dddd, 0x0088dd, 0x0000dd, 0x8800dd, 0xdd00dd, 0xdd88dd, 0xdddddd,
                      0xdddd88, 0xdd8888];
        const geometry = new SphereGeometry(0.5*factor, 8, 8);
        const material = new MeshBasicMaterial({wireframe: true, color: colors[0]});
        const material2 = new MeshBasicMaterial({wireframe: true, color: colors[1]});
        const sphere   = new Mesh(geometry, material);
        const sphere2  = new Mesh(geometry, material2);
        
        let ic = 2;
        this.scene.add(sphere2);
        
        
        sphere.position.set(0,0,0);
        sphere2.position.set(0,0,0);
        sphere2.material.color = new Color(colors[ic]);
        ic += 1;       
        
        const ret = 
            {component: sphere, name: "TheSphere",
        
             render: function(time) {
                 steps -= 1;
                 if (steps < 0) {
                     here.frame.setAnimation(here.toAnim);
                 }
                
                ret.component.scale.setScalar(scale);
                sphere2.scale.setScalar(scale2);
                scale  += sign*0.004;
                scale2 += sign*0.004;
                if (scale <= 0  || scale > 1.01) {                  
                    ic += 1;
                    if (ic > colors.length) {
                        ic = 0;
                    }
                    sphere.material.color = new Color(colors[ic]);
                    if (scale <= 0) {
                        scale = 1.01;
                    } else {
                        scale = 0.01;
                    }
                }
                if (scale2 <= 0  || scale2 > 1.01) {                  
                    ic += 1;
                    if (ic > colors.length) {
                        ic = 0;
                    }
                    sphere2.material.color = new Color(colors[ic]);
                    if (scale2 <= 0) {
                        scale2 = 1.01;
                    } else {
                        scale2 = 0.01;
                    }
                }
             }};
         
        this.scene.add(new AmbientLight(0x333333));

        this.addElement("TheSphere", ret);
        this.finish();
    }
    
    doLongClick(event)  {
   }    
}

export default Transition;
