import Action from "./Action.js";
import Util   from "../../std/Util.js";

class impulse extends Action {
    constructor(ptr, anim, traj) {
        super(ptr, anim, traj);
    }

    execute() {
        let command = this.ptr.elem;
        let flds = command.forward.trim().split(" ");
        let imp;
        
        if (flds.length == 1) {
            let dv    = parseFloat(flds[0]);
            let state = traj.traj.stateVector(time);
            let vn    = Util.unit(state[1]);
            imp = Util.times(dv, vn);
        } else if (flds.length == 3) {
            imp =  [parseFloat(flds[0]), parseFloat(flds[1]), parseFloat(flds[2])];
        } else {
            throw Error("Invalid Impulse request:"+command.toString());
        }
        this.traj.addImpulse(imp, this.time);
    }
}

export default impulse;
  