import Action    from "./Action.js";
import Command   from "../Command.js";
import TransMid  from "../../traj/TransMid.js";
import Util      from "../../std/Util.js"; 
import Constants from "../../std/Constants.js"; 

class transmid extends Action {
    constructor(ptr, anim, traj) {
        super(ptr, anim, traj);
    }
    
    // Transfer from the current orbit to the requested orbit.
    // In terms of actions this simply add generated commands to the queue.
    execute(reverse) {
        if (this.anim.delta > 0) {
            
            this.cleanGenerated();
            this.initiate();
        }
    }
    
    initiate() {
        // Handle two or four tokens: destination and maximum of transfer,
        // initial and final escape velocities (which default to 0)
        let fields = this.ptr.elem.forward.trim().split(" ");
        let dest   = fields[0];
        let durat  = this.parseDuration(fields[1]);
        let ve0 = 0;
        let ve1 = 0;
        if (fields.length == 4) {
            ve0 = Number.parseFloat(fields[2]);
            ve1 = Number.parseFloat(fields[3]);
        }
        if (isNaN(durat)  || durat <= 0) {
            this.anim.setQuickHTML("Illegal transfer duration: ", fields[1]);
        }
        let p0     = this.traj.lastState;
        let seq    = this.traj.sequence;
        let here   = this;
        
        function startTransfer(bodies) {
            if (!bodies || !bodies[0] || !bodies[0].meta || !bodies[0].meta.orbit) {
                here.anim.setQuickHTML("Unable to find destination: "+dest);
                return;
            }
            let currOrb = here.traj.traj;
            let destOrb = bodies[0].meta.orbit;
            let tr    = new TransMid(here.anim.time, here.anim.time+durat, currOrb, destOrb, ve0, ve1);
            let imps  = tr.getImpulses();
            for (let i in imps) {
                let imp     = imps[i];
                let impcmd  = new Command(imp.t, "impulse", imp.dv[0]+" "+imp.dv[1]+" "+imp.dv[2]);
                impcmd.setGenerated(here);
                seq.add(impcmd);
            }
        }
        this.anim.find(dest.trim(), startTransfer);
    }
}

export default transmid;
