import Animation from "../elems/Animation.js";
import Util from "../std/Util.js";
import Dump from "../std/Dump.js";

import {Scene, Vector3, Euler, DirectionalLight, AmbientLight} from "three";
import {MeshBasicMaterial, SphereGeometry, Mesh, InstancedMesh, Matrix4} from "three";

class TestAnimation extends Animation {
    constructor(params) {
        super(params);
        this.name          = "Test";
        this.delta         = 20;
        this.longClickTime = 1000;  // A little shorter since
                                    // we do less clicking here.

        this.scene       = new Scene();
        this.textShowing = true;
        this.centerBody  = "";
        
        this.cameraPos = new Vector3(0,0,2.5);
        this.cameraRot = new Euler(0,0,0,"XYZ");
        this.baseAnimation = "Solar System";
        
        const here = this; 
        
        const sunlight = new DirectionalLight(0xffffff, 1);
        sunlight.position.x = -1000;
        sunlight.position.y = 0;
        sunlight.position.z = 0;
        
        
        this.addElement("sunlight", {name: "sunlight", component: sunlight}, true);                     
        this.scene.add(new AmbientLight(0x333333));

        var radius = 1;
        var geometry = new SphereGeometry(radius, 32, 32);                
        var material = new MeshBasicMaterial({transparent: true, opacity: 0.3, wireframe: false, color: 0xFFFF00});
        var sphere   = new Mesh(geometry, material);
 
        sphere.position.set(1, 1, 0);
        sphere.meta = {name: "galaxy"};
        this.addElement("galaxy", {name: "galaxy", component: sphere});

        material = new MeshBasicMaterial({transparent: true, opacity: 0.3, wireframe: false, color: 0xFF00FF});
        sphere   = new Mesh(geometry, material);
 
        sphere.position.set(-1, -1, 0);
        sphere.meta = {name: "galaxy2"};
        this.addElement("galaxy2", {name: "galaxy2", component: sphere});
        let whiteMaterial = new MeshBasicMaterial({transparent: true, opacity: 0.01, wireframe: false, color: 0xFFFFFF});
        const theBulge = new InstancedMesh(geometry, whiteMaterial, 200);
        for (let i=0; i<200; i += 1) {
            let scl = .005*(i+1);
            let mat = new Matrix4().makeScale(scl, scl, scl);
            mat.setPosition(0,0,0);
            theBulge.setMatrixAt(i, mat);
        }
        this.addElement("bulge", {name: "bulge", component: theBulge}, false);

        this.addTimeDisplay();                       
    }
}

export default TestAnimation;
