import {Component} from "react";
import Util from "../std/Util.js";
import TextInput from "../elems/TextInput";
import CommandParser from "../trajanim/CommandParser.js";

class TrajChooser extends Component {
    
    constructor(args) {
        super(args);
        this.planet = null;
        this.anim   = args.anim;
        this.trajControl = args.trajControl;
    }
    
    // Callback to update value.
    updPlanet(val) {
        this.planet = val;
    }
    
    
    render() {
        return (
    <div>
        <h3>Initiate Trajectory</h3>
        <div>
        Drive rocket manually...<br/>
            <button onClick={this.drive.bind(this)}> Start</button>
            <TextInput  size="8" placeholder="...at..."
               update={this.updPlanet.bind(this)}
               callback={this.drive.bind(this)}
            />
        </div>
        <hr />
        <div>
            ... or read trajectory<br/>
            using Copy/Paste buffer.
            <br/>
            <button onClick={this.playback.bind(this)}>Copy Trajectory</button>
        </div>
    </div>
        );    
    }
    
    drive() {
        // Don't do anything if the start location is not defined.
        if (this.planet) {
            this.anim.find(this.planet, this.startAt.bind(this));
        }                
    }
    
    // Start a manual trajectory assuming we found a starting location.
    startAt(list) {
        if (!list || list.length == 0) {
            this.anim.setQuickHTML("Unable to find object: "+this.planet);
            return;
        } else {
            this.anim.setQuickHTML("");
            let first = list[0];
            if (first.meta.orbit) {
                this.trajControl.startOrbit(first.meta.orbit, this.planet, this.anim.time);
                if (list.length > 1) {
                    this.anim.setQuickHTML("Used first object in list matching "+this.planet);
                }
1            } else {
                this.anim.setQuickHTML("Unable to find orbit in matching object for "+this.planet);
            }
        }
    }
    
    // Parse and start trajectory in paste buffer.
    playback() {
        let here = this;
        let success = function(text) {
            let parser   = new CommandParser();
            let sequence = parser.parse(text);
            let start    = sequence.start();
            this.trajControl.startSequence(sequence);
            here.anim.setQuickHTML("Initiated trajectory");
            Util.log("Read and initiated trajectory from clipboard.");
        }
        
        // This should catch errors in either the clipboard
        // copy or the parsing of the trajectory.
        let failure = function(err) {
            let str = err+"";
            if (str.length > 33) {
                str = str.substr(0,30)+"...";
            }
            // Clean up string a little, but could be more serious here.
            str = str.replaceAll("&", "&amp;");
            str = str.replaceAll("<", "&lt;");
            str = str.replaceAll(">", "&gt;");
            here.anim.setQuickHTML("Unable to copy program: <br>"+str);
        }
        
        navigator.clipboard.readText()
            .then(success.bind(this))
            .catch(failure.bind(this));
    }    
}

export default TrajChooser;
