import Animation from "../elems/Animation.js";
import Util      from "../std/Util.js";
import Coords    from "../std/Coords.js";
import galaxies  from "../data/galaxies.js";
import Constants from "../std/Constants.js";
import {Scene, Vector3, MeshBasicMaterial, 
        SphereGeometry, CylinderGeometry, 
        Quaternion, 
        InstancedMesh, Euler, Matrix4, Color} from "three";

class Galaxies extends Animation {
        
    constructor(params) {
        super(params);
        
        this.delta  = Constants.CENTURY/100;
        this.time   = 0;
        this.paused = true;
        this.name   = "Nearby Galaxies";          
        this.longClickTime = 1500;  // A little shorter since
                                    // we do less clicking here.

        this.scene       = new Scene();
        this.sceneScale  = 1000000*Constants.PC;
        this.textShowing = true;
        this.centerBody  = "Milky Way";
        
        this.cameraPos = new Vector3(0,0,2.5);
        this.cameraRot = new Euler(0,0,0,"XYZ");
        this.baseAnimation = "Abell Clusters";
        this.sceneCoord = "Ga";
        this.minScale = 21.7;
        this.maxScale = 23.5;
        
        const here = this; 
        this.galaxies = galaxies;
        
        function hasDisk(morph_type) {
            return morph_type > -1 && morph_type < 8;
        }
        
        const material = new MeshBasicMaterial({wireframe: false});
        const baseGal  = new SphereGeometry(1, 20, 20);
        const baseRing = new CylinderGeometry(1.4,1.4,.14,20);
        
        let diskIndices = [];
        
        
        for (let i=0; i<galaxies.length; i += 1) {
            if (hasDisk(galaxies[i].morph_type)) {
                diskIndices.push(i);
            }
        }
        
        const theGals  = new InstancedMesh(baseGal, material, galaxies.length);
        const theDisks = new InstancedMesh(baseRing, material, diskIndices.length);
        let diskIndex = 0;
        let axesFix = new Quaternion().setFromAxisAngle(new Vector3(1,0,0), Math.PI/2);
        for (let i=0; i<galaxies.length; i += 1) {
            
            let gal = galaxies[i];
            let cores = [1, 0.9,0.75,0.6,0.5, 0.4, 0.3, 0.2];
            let core = 1;
            if (hasDisk(gal.morph_type)) {
                core = cores[gal.morph_type];
            }
            let scale = gal.diameter/1000;
            let matc = new Matrix4().makeScale(core*scale, core*scale,core*scale);

            matc.setPosition(gal.x, gal.y, gal.z);            
            theGals.setMatrixAt(i, matc);

            if (hasDisk(gal.morph_type)) {
                let pole = [0,90];
                if (gal.pa  && gal.inclination) {
                    let dtor = Math.PI/180;
                    let l = gal.l*dtor;
                    let b = gal.b*dtor;
                    let inc = gal.inclination*dtor;
                    let pa  = gal.pa*dtor;
                    // The position angle of the galaxy is the long axis, but that's perpendicular to the
                    // axis along which the axis would be moving as we change the inclination, so we add
                    // 90 degress to the pa.
                    pa += Math.PI/2;
                    if (pa > Math.PI) { 
                        // Keep in 0-180 degree range
                        pa = 2*Math.PI - pa;
                    }
                    let nb = Math.PI/2-b;
                    let np = Math.cos(Math.PI/2-b)*Math.cos(inc) + Math.sin(Math.PI/2-b)*Math.sin(inc)*Math.cos(pa);
                    if (Math.abs(np) > 1) {
                        Util.log("Invalid angle:"+np);
                    }
                    np = Math.acos(np);
                    let flag = false;
                    if (np > Math.PI) {
                        np = 2*Math.PI-np;
                        flag = true;
                    }
                    let bp = Math.PI/2 - np;                    
                    
                    let dlp = (Math.cos(inc) - Math.cos(np)*Math.cos(nb)) / (Math.sin(np)*Math.sin(nb));
                    if (Math.abs(dlp) > 1) {
                        Util.log("Unexpected values adding disk in Nearby Galaxies:"+dlp, Math.cos(inc),Math.cos(np),Math.cos(nb),Math.sin(np),Math.sin(nb));
                    } else {
                        dlp = Math.acos(dlp);
                        if (flag) {
                            dlp = -dlp;
                        }
                    }
                    pole = [l+dlp, bp];
                }
                let sv      = new Vector3(scale, scale, scale);
                let pv      = new Vector3(gal.x, gal.y, gal.z);
                let quatern = new Quaternion();
                quatern.setFromEuler(new Euler(pole[0], Math.PI/2-pole[1], 0, "ZYX"));
                quatern.premultiply(axesFix);
                let mat  = new Matrix4().compose(pv, quatern, sv);
                theDisks.setMatrixAt(diskIndex, mat);
                theDisks.setColorAt(diskIndex, new Color(0xaabbcc));
                diskIndex += 1;
            }
            
            if (gal.morph_type < -1) {
                theGals.setColorAt(i, new Color(0xFF7777));
            } else if (gal.morph_type < 2) {
                theGals.setColorAt(i, new Color(0xFFFF77));
            } else if (gal.morph_type < 4) {
                theGals.setColorAt(i, new Color(0xFFFFFF));
            } else if (gal.morph_type < 6) {
                theGals.setColorAt(i, new Color(0x77FFFF));
            } else if (gal.morph_type < 8) {
                theGals.setColorAt(i, new Color(0x7777FF));
            } else {
                theGals.setColorAt(i, new Color(0x0000FF));
            }
            this.bodyCount += 1;
        }
        this.addElement("galaxies", {name: "galaxies", component: theGals,
            render: 
                function(time) {
                }});
        theGals.getIndexedMeta = function(index) {
            let gal = this.galaxies[index];
            let meta = {...gal, type:"galaxy"};
            return meta;
        }.bind(this);
        this.addElement("disks", {name: "disks", component: theDisks,
            render: 
                function(time) {
                }});
        theDisks.getIndexedMeta = function(index) {
            let gal = this.galaxies[diskIndices[index]];
            let meta = {...gal, type:"galaxy"};
            return meta;
        }.bind(this);
        this.addTimeDisplay();
        this.finish();
    }
    
    getAnimationFrame() {
        return Coords.getCoords("galec");
    }
        
    find(name, callback) {
        this.frame.finder.getLocation(name, this.galaxies, "galaxy", callback);
    }
    
    doLongClick(e) {
        const matched = this.frame.raycast(e);
        
        for (let i=0; i<matched.length; i += 1) {
            let entry = matched[i];
            let name = entry.meta.name;
            if (entry.meta.name == "Milky Way") {                
                this.setAnim("Local Galactic Neighborhood");
                return;
            }
        }
        super.doLongClick(e);
    }
    


}

export default Galaxies;

