import Util from "../std/Util.js";
import CoordTrans from "../std/Coords.js";
import Constants from "../std/Constants.js";
import Dump from "../std/Dump.js";
import galaxies from "../data/galaxies.js";

// Find the position of an object in space.  There are three sources for
// where the object may be:
// 
//    1. It may be an instantiated object in the simulation where there is
//       an associated image element that can be returned for it (planets, moons)
//    2. It may be in a list of objects which are rendered as a unit in the
//       current animation (small bodies in the solar system, stars in the local
//       galactic neighborhood, artificial Earth satellites).
//    3. It may be an object found using the SIMBAD name service for which both
//       a position and distance can be identified.  If the standard Simbad object name matches
//       an entry in our catalog of nearby galaxies, we use the distance from that
//       since the velocity of the galaxy is likely dominated by the local flow.
//       In this case, the user should be careful to understand the scale
//       of the distance unit relative to the scale of the currrent animation.

class GenFinder {
    // Simbad classes that we treat as Galaxies or larger.
    static exgals =  [// First all Galaxy objects
                 "G", "PoG","GiC", "BiC", "GiG", "Gip",
                 "rG", "H2G", "LSB", "AG?", "Q?",
                 "Bz?", "BL?", "EmG", "SBG", "bCG",
                 "LeI", "LeG", "LeQ", "AGN", "LIN",
                 "SyG", "Sy1", "Sy2", "Bla", "BLL",
                 // Extragalactic multiples
                 "OVV", "QSO", 
                 "SCG", "ClG", "GrG", "CGG", "PaG",
                 "IG"];
    
    constructor(frame) {      
        this.frame       = frame; // Use same finder for all animations.
        this.galNames    = null;
        this.simbadCache = {}; 
    }
        
    // This may either return after calling the callback
    // or run the callback asynchronously later, so no further
    // processing (at least any that depends upon this result)
    // should be done after invoking this routine.
    getLocation(name, list, type, callback) { 
        
        const nameLC = name.toLowerCase();
        if (nameLC == "sun"  || nameLC == "sol") {
            // Should we fix this for moons and planets?  (i.e., Sun is not at origin)
            const star = {
                id: 0, type: "star", name:"Sol", x:0, y:0, z:0, d:0, spt: "G2V", u:0, v:0, w:0, np: 9                
            };
            callback([{meta: star, distance: 0}]);
            return;
        }
        
        // At each level, the function called may
        // have consumed the callback and will return true.
        // If not, then we go to the next level.
        if (!this.getFromLocal(name, list, type, callback)) { 
            if (!this.getFromCache(name, callback)) {
                // This will always invoke the callback (after an XHR request)
                // so we don't need to do anything further.
                this.tryFromSimbad(name, callback);
            }
        }
    }
    
    // This compares the name to the individual components of the current animation.
    getFromComponents(name, match) {
        let found = [];
        let elements = this.frame.animation.sceneElements;
        for (let name in elements) {
            if (elements[name].notBody) {
                continue;
            }
            
            let test = name;
            if (name.endsWith("Body")) {
                test = test.substring(0, test.length-4);
            }
            if (name.endsWith("Orbit")) {
                continue;
            }
            if (name.match(/.+(Body|Orbit)\d+$/)) {
                // Skip generated objects
                continue;
            }
            if (test.match(match)) {
                let comp = elements[name].component;
                found.push({component: comp, meta: comp.meta, distance: 0});
            }
        }
        return found;        
    }
    
    // This looks at the elements of a list (e.g., asteroids, satellites, stars)
    getFromList(name, type, found, list, match) {
        
        for (var i = 0; i < list.length; i += 1) {
            const obj = list[i];
            if (obj.name.match(match)) {
                let got = {...obj};
                if (!obj.type) {
                    got.type = type;
                }
                found.push({meta:got});
            } else if (obj.id && (name == ("" + obj.id) || name == ("HIP " + obj.id))) {
                let got = {...obj};
                if (!obj.type) {
                    got.type = type;
                }
                found.push({meta:got});
            }
        }
    }
    
    // This gets the information from local (non-SIMBAD) sources.
    // This method returns directly rather than using the call back,
    // so it can be invoked when we want to process synchronously.
    getLocalList(name, list, type) {
    
        let input = name.toLowerCase();
        if (input.indexOf("*") >= 0) {
            input = input.replaceAll("*", ".*");
        }
        // Probably other characters to be wary of, but parens are the
        // ones we know cause trouble...
        input = input.replaceAll("(", "\\(");
        input = input.replaceAll(")", "\\)");
        let match = new RegExp("^"+input+"$", "i");
        let found = this.getFromComponents(name, match);
        if (list != null) {
            this.getFromList(name, type, found, list, match);            
        }
        return found;
    }
    
    // This gets information from local sources and
    // calls the callback if anything was found.  Its
    // return value indicates whether it has called the callback.
    getFromLocal(name, list, type, callback) {
        let found = this.getLocalList(name, list, type);
        
        if (found.length > 0) {
            callback(found);
            return true;
        }
        return false;
    }
    
    // This looks for cached information from SIMBAD
    // (so we call SIMBAD only once for a given object)
    // If found in the cache, the callback is called.
    // Returns true/false if the callback was/was not called.
    getFromCache(name, callback) {
        if (this.simbadCache[name]) {
            callback([this.simbadCache[name]]);
            return true;
        }
        return false;
    }
    
    // This makes an XML HTTP request to SIMBAD.
    // If we call this, then the callback will be invoked
    // after the XHR request is resolved.
    tryFromSimbad(name, callback) {
        const xhr = new XMLHttpRequest();
        const here = this;
        const xhrHandler = function() {
            if (xhr.readyState === XMLHttpRequest.DONE) {
                const simb = here.parseSimbad(name, xhr.responseText);
                if (simb) {
                    here.simbadCache[name] = simb;
                    callback([simb]);
                } else {
                    callback( [] );
                }
            }
        };
        
        const nmenc = encodeURIComponent(name);
        // SIMBAD supports CORS requests.
        const url   = 
                "https://simbad.u-strasbg.fr/simbad/sim-id?Ident=" +        
                nmenc +
                "&NbIdent=1&Radius=.02&Radius.unit=arcmin&submit=submit+id&output.format=ascii&obj.bibsel=off";
        
        xhr.open('GET', url);
        
        Util.log("SIMBAD Query:"+url);
        xhr.onreadystatechange = xhrHandler;
        xhr.send();  // parseSimbad will be called when we get the data.        
    }
    
    getGalMatch(name) {
        if (!this.galNames) {
            this.setGalNames();
        }
        return this.galNames[name];        
    }
    
    setGalNames() {
        this.galNames = {};
        for (let i=0; i<galaxies.length; i += 1) {
            let gal = galaxies[i];
            this.galNames[gal.name] = gal;
        }
    }
   
    parseSimbad(name, text) {
        
        const lines = text.split("\n");
        let tokens;
        
        const getTok = function(n) {
            let res = null;
            if (tokens.length > n) {
                res = tokens[n];
                if (res == "~") {
                    res = null;
                }
            }
            return res;
        }
        
        let data = {};
        data.name = name;
        data.d    = null;
        data.l    = null;
        data.b    = null;
        
        data.pmr  = -1.e10;
        data.rv   = -1.e10;
        data.pmd  = -1.e10;
        let type  = "unknown";
        
        var inIds = false;
        for (var i=0; i<lines.length; i += 1) {
            const line = lines[i];
            tokens = line.split(/ +/);
            if (!inIds) {
                if (line.startsWith("Object")) {
                    let flds = line.substring(7).split("---");
                    let name = flds[0].trim();
                    if (name.startsWith("NAME ")) {
                        name = name.substring(5);
                    }
                    // Match against nearby galaxies whose
                    // distance is known but not derivable
                    // from either parallax or redshift.
                    let match = this.getGalMatch(name);
                    if (match) {
                        return {meta:{...match, type:"galaxy", du:"Mpc"}};
                    }
                    
                    data.simClass = flds[1].trim();
                    
                }
                if (line.startsWith("Coordinates(Gal")) {
                    if (getTok(1) != null) {
                        data.l = getTok(1);
                        data.b = getTok(2);
                    }
                } else if (line.startsWith("Parallax:")) {
                    if (getTok(1) != null) {
                        data.d = 1000./Number.parseFloat(getTok(1));
                        data.du = "pc";
                    }
                } else if (line.startsWith("Proper motions:")) {  // Note space in label
                    if (getTok(2) != null) {
                        data.pmr = getTok(2);
                        data.pmd = getTok(3);
                    }
                } else if (line.startsWith("Radial Velocity:")) {
                    if (getTok(2) != null) {
                        data.rv = getTok(2);
                    }
                } else if (line.startsWith("Spectral type:")) {
                    if (getTok(2) != null) {
                        data.spt = getTok(2); 
                        type = "star";
                    }
                    
                } else if (line.startsWith("Morphological type:")) {
                    if (getTok(2) != null) {
                        data.morph = getTok(2);
                        type = "galaxy";
                    }
                    
                } else if (line.startsWith("Identifier")) {
                    inIds = true;
                }
                
            } else {
                if (this.setHIP_ID(line, data)) {
                    break;
                }
            }
        }
        
        // Don't trust parallaxes/pm's to extragalactic objects.
        // We can still sometimes get their distances from the
        // radial velocity.
        let extra = false;
        if (data.simClass && GenFinder.exgals.includes(data.simClass)) {
            extra = true;
        }
        if (extra || type == "galaxy") {
            data.d = null;
            data.du = null;
            data.u = null; data.v = null; data.w = null;
        }
        
        
        // Get the distance (in Mpc) for objects if
        // we don't currently have a distance and the
        // redshift seems sufficient to suggest a cosmological
        // distance.
        if (!data.d && data.rv && (data.rv > 1000) ) {
            data.d = data.rv/ Constants.H_0;
            data.du = "Mpc";
        }
        
        if (data.l && data.b && data.d) {
            var l = Math.PI*data.l/180;
            var b = Math.PI*data.b/180;
            var d = data.d;
            // Calculate x,y,z from l,b,d.  
            data.x = d*Math.cos(l)*Math.cos(b); 
            data.y = d*Math.sin(l)*Math.cos(b); 
            data.z = d*Math.sin(b);
            data.type = type;
            
            if (data.pmr > -1.e9 && data.pmd > -1.e9 && data.rv > -1.e9 ) {
                // Calculate u,v,w from pm and radial velocity
                // 
                // Convert mas/yr -> radians/s
                let scl = .001/3600 * Math.PI/180 * 100/Constants.CENTURY;
                // Convert pc -> meters
                let dm  = d*Constants.PC;
                let vr = 1000*data.rv;
                
                // PM's are given in equatorial coordinates.
                let galeq = CoordTrans.getCoords("galeq");
                let radec = galeq.transform(l,b);
                let ra = radec[0];
                let dec = radec[1];
                let sr = Math.sin(ra);
                let cr = Math.cos(ra);
                let sd = Math.sin(dec);
                let cd = Math.cos(dec);
                let v = [
                    vr*cd*cr - dm*sd*cr*data.pmd*scl - dm*cd*sr*data.pmr*scl,
                    vr*cd*sr - dm*sd*sr*data.pmd*scl + dm*cd*cr*data.pmr*scl,
                    vr*sd    + dm*cd*data.pmd*scl                   
                ];
                // This is the velocity in equatorial coordinates.  Need to translate
                // back to galactic.
                let eqgal = CoordTrans.getCoords("eqgal");
                let vg = eqgal.vTransform(v);
                
                // Elsewhere we have this in km/s, not m/s.  
                data.u = vg[0]/1000;
                data.v = vg[1]/1000;
                data.w = vg[2]/1000;
            
            } else {
                // Get rid of fillers.
                data.pmr = null;
                data.pmd = null;
                data.rv  = null;
            }
        }
        
        if (data.x == null) {
            Util.log("Simbad query returning null");
            return null;
        }
        return {meta:{...data, type: type}};
    }
    
    setHIP_ID(line, data) {
        for (var i=2; i<line.length; i += 32) {
            if (i+30 <= line.length) {
                var fld = line.substring(i, i+30).trim();
                if (fld.startsWith("HIP ")) {
                    data.id = fld.substring(4);
                    return true;
                }
            }
        }
        return false;
    }
}

export default GenFinder;