import {Component, createRef} from "react";

import {PerspectiveCamera, Matrix4, WebGLRenderer, Quaternion, Euler, Vector3, Group} from "three";
import {Scene} from "three";
import Rotater from "../three/Rotater.js";

import Rocket from    "../components/Rocket.js";
import CoordSys from "../components/CoordSys.js";
import Util      from "../std/Util.js";
import Constants from "../std/Constants.js";
import PushButton from "../elems/PushButton.js";
import Command from "../trajanim/Command.js";

class RocketController extends Component {
    
    constructor(args) {
        super(args);
        this.anim         = args.anim;
        this.controllerOff    = args.controllerOff;
        this.camRad       = 4;
        this.trajControl  = args.trajControl;
        this.thrusting    = false;
        this.wasThrusting = false;
        this.savedArgs    = null;
        this.camera       = null;
        this.state        = {backward: args.anim.delta < 0, dv: 0};
        this.rocket       = null;
        this.controls     = null;
        this.vAbs         = [0,1,0];
        this.count        = 0;
        
        // Put this here so both DidMount and WillUnmount can use same bound function.
        //   x.bind(this) != x.bind(this) so unmount won't work if we bind in those functions.
        this.timeHandler = this.timeReverse.bind(this);
    }
    
    // Defer setting up three.js controls until
    // we have a real component to work with.
    componentDidMount() {
        
        let elem          = document.getElementById("ControlDisp");
        const scene       = new Scene();
        const camera      = new PerspectiveCamera( 70, 1, 0.1, 10 );
        this.camera       = camera;
        camera.position.z = this.camRad;        
        const renderer    = new WebGLRenderer({antialias: true});
        this.renderer     = renderer;
        renderer.setClearColor("#000000");
        renderer.setSize(100, 100);
        this.lastAngle = -100;
                
        elem.appendChild(renderer.domElement);
        
        this.trajControl.setController(this);
 
        this.rocket = new Rocket({height:1.5, name:"Demo"});
        this.rocket.rotation.x = -Math.PI/2;
        let comp = new Group();
        comp.add(this.rocket);

        scene.add( comp );
        const csys = new CoordSys();
        scene.add(csys);
        elem.appendChild(renderer.domElement);
        this.rocket.Mfire.visible = this.thrusting || this.trajControl.thrusting;
        this.setControls();
        this.anim.frame.renderer.domElement.addEventListener("timereverse", this.timeHandler);
        
        this.startRender(renderer, camera, scene, this.rocket, csys);
    }    
    
    componentWillUnmount() {
        this.trajControl.rocketControl = null;
        this.anim.frame.renderer.domElement.removeEventListener("timereverse", this.timeHandler);
    }

    // Set the controls for the manual orientation.
    setControls() {
        if (this.controls) {
            // New set of controls.
            this.controls.dispose();
        }
        this.controls = new Rotater(this.rocket, this.renderer.domElement);
    }
    
    // Close out the trajectory from the animation.
    closeTraj() {
        this.anim.frame.trajControl.delRocket();
        this.anim.frame.trajControl = null;
        this.anim.frame.userControls.holder.setState({optState: false, content: null});
    }

    // Called after a deferred start is finally ready to show up.
    restart() {
        if (this.savedArgs == null) {
            throw new Error("Attempt to restore RocketController without saved data");
        }
        let sv = this.savedArgs;
        this.startRender(sv.renderer, sv.camera, sv.scene, sv.rocket, sv.csys);
        this.savedArgs = null;
    }
    
    timeReverse() {
        this.setState({backward: this.anim.delta < 0});
    }
    
    // Begin rendering the rocket controller.
    startRender(renderer, camera, scene, rocket, csys) {
        let here = this;
        let count = 0;
        let lastQuat = null;
        
        var render = function () {
            requestAnimationFrame( render );
            
            if (here.props.controllerOff) {
                let svArgs = {renderer: renderer, camera:camera,  
                              scene: scene, rocket: rocket, csys: csys};
                here.savedArgs = svArgs;
                return;
            }
            
            here.controls.update();
            let animCamera = here.anim.frame.camera.position;
            let rockState = here.trajControl.lastState;
//            if (count % 100 == 0) {
//                console.log("Rocket state:", rockState);
//            }
            if (rockState == null) {
                return;
            }
            
            let cp   = here.anim.frame.camera.position;
            let cpos = [cp.x,cp.y,cp.z];
            let rpos = Util.times(1/Constants.AU, rockState[0]);
            let vel  = rockState[1];
            let cmp  = Util.viewedCoordFrame(cpos, rpos, vel);
            let x    = Util.vec3(cmp[0]);
            let y    = Util.vec3(cmp[1]);
            let z    = Util.vec3(cmp[2]);
            
            
            // These unit vectors define a coordinate system relative to
            // the input frame.  Csys is meant to represent the global
            // coordinate frame, so we use the inverse to properly
            // orient it.
            let mat = new Matrix4();
            mat.makeBasis(x,y,z);
            csys.setRotationFromMatrix(mat.transpose());            
            
            rocket.Mfire.visible = here.thrusting  && here.trajControl.thrusting;
                        
            // Render the scene
            renderer.render(scene, camera);
            let cr = rocket.rotation;
            let v = new Vector3(0,0,1);
            v.applyEuler(rocket.rotation);
            v.applyMatrix4(mat.transpose());
            here.vAbs = v;
//            if (count % 100 == 0) {
//                console.log("Controller Rocket state:", rockState, v);
//            }
            count += 1;
            
        };
        render();
    }
    
    // HTML for the rocket controller.
    render() {  
        
        return  (      
<div>
  <table><tbody>
    <tr><td>
      <table visible={""+!this.controllerOff}><tbody>
        <tr><td colSpan="3" align="center" onClick={this.rev.bind(this)}>R</td></tr>
        <tr>
        <td onClick={this.right.bind(this)}><h3>+</h3></td>
          <td><span id="ControlDisp" style={{width:"50px",height:"50px"}}></span></td>
          <td onClick={this.left.bind(this)}><strong>-</strong></td></tr>
        <tr><td colSpan="3" align="center" onClick={this.fwd.bind(this)}>F</td></tr>
      </tbody></table>        
    </td><td>
      <PushButton
            disabled={this.state.backward  || this.state.controllerOff}
            label="Thrust" 
            down={this.thrustPushed.bind(this)}
            up={this.thrustUp.bind(this)}
      /><br/>
      <button onClick={this.copyTraj.bind(this)}>Save trajectory</button><br/>
      <button onClick={this.closeTraj.bind(this)}>Close trajectory</button>
    </td></tr>
   </tbody></table>
   <span>dV: {this.state["dv"].toFixed(1)}</span>
</div>
        );
    }
    
    // Directional buttons.
    fwd() {
        this.setOrient(-Math.PI/2, 0);
    }
    rev() {
        this.setOrient(-Math.PI/2, Math.PI);
    }
    right() {
        this.setOrient(-Math.PI/2, Math.PI/2);
    }
    left() {
        this.setOrient(-Math.PI/2, -Math.PI/2);
    }
    
    // Called after current trajectory is successfully pasted
    // to clipboard. 
    copySuccess() {
        this.anim.setQuickHTML("Trajectory copied to clipboard. "+this.trajControl.sequence.size+" elements");
    }
    
    // Error in clipboard copy.
    copyFailure(err) {
        this.anim.setQuickHTML("Trajectory copy failed:"+err);
    }
    
    // Attempt to copy the current trajectory sequence to the clipboard.
    copyTraj() {
        navigator.clipboard.writeText(this.trajControl.sequence.toString())
          .then(this.copySuccess.bind(this), this.copyFailure.bind(this));
    }
    
    
    // Set the orientation of the camera.
    setOrient(x,y){
        let cr = this.rocket.rotation;
        cr.x = x;
        cr.y = y;
    }
    
    // Called only when a new push is seen.
    // Initiates a THRUST command.
    // We add a slight delay to the thrust so that it gets executed in the
    // next segment.
    thrustPushed() {
        if (!this.wasThrusting) {
            let r = this.rocket.rotation;
            // We add the PI/2 so that we get an x-angle of 0 for the
            // thursts in the view plane.
            let cmd = new Command(this.anim.time+this.anim.delta*1.e-6,"thrust", ".001 "+(r.x+Math.PI/2)+" "+r.y);
            this.trajControl.sequence.add(cmd);
        }
        this.thrusting    = true;
        this.wasThrusting = true;
    }
    
    // The thrust button is let go. 
    // Initiates an ORBIT command.
    thrustUp() {
        if (this.wasThrusting) {
            let cmd = new Command(this.anim.time+this.anim.delta*1.e-6,"orbit", null, null);
            this.trajControl.sequence.add(cmd);
        }
        this.thrusting    = false;
        this.wasThrusting = false;
    }
}

export default RocketController;

