import Util from "../std/Util.js";
import TimeFormatter from "../elems/TimeFormatter.js";
import Acts from "./actions/Acts.js";

class Command {
    constructor(time, command, forward) {
        // The instant the command should happen/start (going forward)
        this.t = time;
        
        // The keyword defining the command.
        this.command = command;
        
        // The arguments to the command when moving forward.
        this.forward    = forward;
        this.generated  = false;
    }
    
    setGenerated(parent) {
        this.generated = parent.ptr.elem;
    }
    
    static count = 0;
    
    // ptr  -- Pointer to the current command in the trajectory sequence.
    //         This also has pointers to the previous and subsequent commands.
    // anim -- The current animation.
    // traj -- The trajectory controller.
    static enact(ptr, anim, trajControl) {
        let cmd = ptr.elem.command;
        let Aclass = Acts[cmd];
        new Aclass(ptr, anim, trajControl).execute();
    }
        
    toString() {
        let tf = new TimeFormatter();
        let time = tf.dateFormat(this.t, 1);
        let res = time + " "+this.command;
        if (this.forward) {
            res += " "+this.forward;
        }
        if (this.generated) {
            return "#GEN "+res;
        } else {
            return res;
        }
    }
}

export default Command;