import Action from "./Action.js";
import ThrustAndGrav    from "../../math/ThrustAndGrav.js";
import ThrustByGradient from "../../math/ThrustByGradient.js";
import TrajGradient     from "../../traj/TrajGradient.js";
class thrust extends Action {
    constructor(ptr, anim, traj) {
        super(ptr, anim, traj);
    }
    
    // Going forward initiate continuous thrust in the direction specified.
    // Going backward go into the orbit defined by the current state.
    execute(reverse) {
        let delta = this.anim.delta;
        if (delta > 0 || reverse) {
            let flds = this.ptr.elem.forward.trim().split(/\s/);
            let mag  = parseFloat(flds[0]);
            
            // Thrust along velocity axis
            if (flds.length == 1) {
                let force = new ThrustAndGrav(this.anim.frame.camera, mag, 0, 0);
                this.traj.setToIntegrate(this.time, force);
            
            // Thrust to match a goal orbit by minimizing a gradient function
            } else if (flds.length == 2) {
                let target = flds[1];
                let here = this;
                let foundGoal = function(bodies) {
                    if (!bodies || bodies.length == 0) {
                        here.anim.setQuickHTML("Unable to resolve body: "+target);
                    } else if (!bodies[0].meta.orbit) {
                        here.anim.setQuickHTML("No orbit known for body: "+target);
                    } else {
                        let goal  = bodies[0].meta.orbit;
                        let grad  = new TrajGradient(goal);
                        // We invert the force since we want to minimize the function.
                        // If we wanted to maximize it we'd use a positive value.
                        let force = new ThrustByGradient(-mag, grad);
                        here.traj.setToIntegrate(here.time, force);
                    }
                };
                this.anim.find(target, foundGoal);
             
            // Thrust in some direction relative to velocity    
            } else if (flds.length == 3) {
                let force = new ThrustAndGrav(this.anim.frame.camera, mag, 0, 0);
                let x = parseFloat(flds[1]);
                let y = parseFloat(flds[2]);
                this.traj.setToIntegrate(this.time, force);
            } else {
                throw new Error("Illegal THRUST command: "+this.elem.toString());
            }
        } else {
            // Must be in an orbit state before, so we just get the current state
            // and create an orbit.
            this.traj.setToOrbit(this.time);
        }
    }
}

export default thrust;

