import Sequence  from "../trajanim/Sequence.js";
import Command   from "../trajanim/Command.js";
import Util      from "../std/Util.js";
import Constants from "../std/Constants.js";

class CommandParser {
    constructor() {
        this.t        = -1.e100;
        this.sequence = new Sequence();         
    }
    
    parse(input) {
        let rows = input.split("\n");
        for (let i=0; i<rows.length; i += 1) {
            rows[i] = rows[i].trim();
            // Note that there may be several continuation lines in a row.
            while(rows[i].endsWith("+")) {
                if (i < rows.length-1) {
                    // Concatenate with the next row
                    let newrow = rows[i].substring(0, rows[i].length-1) + rows[i+1].trim();
                    rows.splice(i,2,newrow);
                } else {
                    rows[i] = rows[i].substring(0, rows[i].length-1);
                }
                
            }
        }
        for (let i=0; i<rows.length; i += 1) {
            this.addLine(rows[i]);
        }
        return this.sequence;
    }
    
    addLine(row) {
        let cmd   = this.parseLine(row);
        if (cmd != null) {
            this.sequence.add(cmd);
        }
    }
    
    parseLine(row) {
        
        // Save comments as a comment command with current time.
        if (row.length == 0 || row.substring(0,1) == '#') {
            return null;
        }
        
        // Split row at whitespace        
        let tokens = row.split(/\s+/);
        if (tokens.length < 2) {
            throw new Error("Missing command-row["+i+"]: "+rows[i])
        }
        
        // Get the date from the first token.
        let date = this.parseTime(tokens[0]);
        if (!date) {
            throw new Error("Invalid date-row["+i+"]: "+rows[i])
        }
        
        // Get the command from the second token
        let cmd  = tokens[1].toLowerCase();
        let args = "";
        let div  = "";
        
        // Concatenate the other arguments back for the forward arguments.
        if (tokens.length > 2) {
            for (let i=2; i<tokens.length; i += 1) {
                args += div+tokens[i];
                div = " ";
            }
        }
                
        // Now create the command!
        let cmdObj = new Command(date, cmd, args);
                        
        this.t = date;
        return cmdObj;
    }
    
    parseTime(str) {
        // Handle relative dates.
        str = str.trim().toLowerCase();
        // Handle relative dates here before trying absolute dates.
        if (str.substring(0,1) == "+") {
            if (!this.t) {
                throw new Error("Relative date used before time frame established");
            }
            let scale  = 1;
            if (str.endsWith("y")) {
                scale  = Constants.CENTURY/100;
                offset = 20*Constants.CENTURY;                 
            } else if (str.endsWith("d")) {
                scale = 86400;
            }
            
            let res = parseFloat(str);
            // parseFloat ignores trailing junk like the time suffix.
            return this.t + scale * res;
        } else {
            return Util.parseTime(str);
        }        
    }    
}

export default CommandParser;

