import {Component} from "react";

import Util from "../std/Util.js";
import Dump from "../std/Dump.js";
import Constants from "../std/Constants.js";
import CoordTrans from "../std/Coords.js";
import TextInput  from "../elems/TextInput.js";
import OptionsSelector from "../elems/OptionsSelector.js";

import {Line,LineBasicMaterial, BufferGeometry, Vector3} from "three";


class SepBoxes extends Component {
    constructor(props) {
        super(props);
        this.entry = props.entry;
        this.anim  = props.anim;
        this.row   = props.row;
        this.from  = this.anim.centerBody;
        this.state = {buttonColor: "white"};
        
        let name   = props.entry.meta.name;
        this.to    = name;
        this.selectedEntries = {name: this.entry};
    }
    
    render() {
        let entry = this.entry;   
        
        return ( 
            <div>
               <TextInput size="8" id="fromBox" update={this.updateFrom.bind(this)} size="8" id="fromBox" defaultValue={this.from} placeHolder=" ..from.. " /> to
               <TextInput size="8" id="toBox"   update={this.updateTo.bind(this)}   defaultValue={this.to}   placeHolder =" ..to.. " />
               <button onPointerDown={this.connect.bind(this)}
                       onPointerUp={this.clearConnect.bind(this)}
                       style={{"backgroundColor":this.state.buttonColor}}
               > Connect </button>
            </div>
        );
    }
    
    updateFrom(val) {
        this.from = val;
    }
    updateTo(val) {
        this.to = val;
    }

    connect() {
        let here  = this;

        if (!this.from || !this.to) {
            this.anim.setQuickHTML("<br>Missing from/to values");
            return;
        }
        
        let fVal = this.from;
        let toVal = this.to;

        function step1(fromEntries) {
            if (fromEntries.length == 0) {
                here.anim.setQuickHTML("<br>Unable to find "+fVal);
                return;
            } else if (fromEntries.length > 1) {
                here.anim.setQuickHTML("<br>"+fVal+" ambiguous (use <b>Find</b> object to select)");
                return;
            }

            function step2(toEntries) {
                if (toEntries.length == 0) {
                    here.anim.setQuickHTML("<br>Unable to find "+toVal);
                    return;
                } else if (toEntries.length > 1) {
                    here.anim.setQuickHTML("<br>"+toVal+" ambiguous (use <b>Find</b> object to select)");
                    return;
                }

                here.drawConnection(fromEntries[0],toEntries[0]);
            }
            if (here.selectedEntries[toVal]) {
                step2([here.selectedEntries[toVal]]);
            } else {
                here.anim.find(toVal, step2);
            }
        }
        if (here.selectedEntries[fVal]) {
            step1([here.selectedEntries[fVal]]);
        } else {
            here.anim.find(fVal, step1);
        }
    }

    drawConnection(from,to) {
        let time = this.anim.time;
        if (this.anim.savedLine) {
            this.clearConnect();
        }
        
        let scale = this.anim.sceneScale;
        let v1 = this.getPosition(from, scale);
        let v2 = this.getPosition(to, scale);
        const vertices = [v1,v2];
        const material = new LineBasicMaterial({color: 0x0000ff});
        const geometry = new BufferGeometry().setFromPoints( vertices );
        const line = new Line( geometry, material );
        let name = "Connect" + OptionsSelector.index;
        OptionsSelector.index  += 1;
        this.anim.addElement(name, {name: name, component: line});
        this.anim.setQuickHTML(this.offsetInfo(v1, v2));
        this.anim.savedLine = name;
        this.setState({buttonColor: "red"});
    }

    offsetInfo(v1, v2) {
        let delta = v2.clone();
        delta.addScaledVector(v1, -1);
        let dist = delta.length();

        let x = delta.x;
        let y = delta.y;
        let z = delta.z;

        let lon = Math.atan2(y, x);
        if (lon < 0) {
            lon += 2*Math.PI;
        }
        let lat = Math.asin(z/dist);

        let scl  = this.anim.sceneScale;
        dist = dist*scl; // Convert to km.
        if (dist > 100000*Constants.PC) {
            dist /= 1000000*Constants.PC;
            scl = "Mpc";
        } else if (dist > 0.1*Constants.PC) {
            dist /= Constants.PC;
            scl = "pc";
        } else if (dist > 0.1*Constants.AU) {
            dist /= Constants.AU;
            scl = "AU";
        } else {
            dist /= 1000;
            scl = "km";
        }
        let iFrame = this.anim.sceneCoord;
        let coords = CoordTrans.coordsSet(iFrame, lon, lat, this.anim.eqPeqE);
        let retStr = "Offset: "+dist.toPrecision(4)+" "+scl+"<br/>Direction: ";
        retStr += CoordTrans.posString(coords);
        return retStr;
    }
    
    clearConnect() {
        this.anim.removeElement(this.anim.savedLine);
        this.anim.savedLine = null;
        this.setState({buttonColor: "white"});
    }

    getPosition(entry, animScale) {
        
        if (entry.component) {
            return entry.component.position;
        }
        
        let v = entry.meta;
        let pos = [0,0,0];
        // Position and velocity (must be star)
        if ((this.anim.time != null) && (v.u != null) && (v.v != null) && (v.w != null)) {
            let time = this.anim.time;
            let scl = time/30.856e12;
            pos =  [v.x+scl*v.u, v.y+scl*v.v, v.z+scl*v.w];

        // Just position
        } else if (v.x != null && v.y != null && v.z != null) {
            pos = [v.x, v.y, v.z];
            
        // Derived from orbit
        } else if (entry.meta.orbit) {
            let time = this.anim.time;
            pos = entry.meta.orbit.position(time);
        }
        
        // Now see if we have to worry about scaling...
        if (v.du) {  // du can be pc (for star/Galactic object) or Mpc (galaxy/extragalactic) 
            if (v.du != animScale) {  // If they are the same, then no problem.
                if (animScale == "Mpc" ) {
                    // Since animation is at largest scale, we
                    // the position should be scaled down.
                    pos = Util.times(1/1000000., pos);  // Convert pc to Mpc
                    
                } else {
                    // We must have a star/galaxy/cluster in a solar system animation,
                    // or a galaxy/cluster in a star simulation.
                    // Convert pc to AU.  Ratio might be much bigger, but we should
                    // only be seeing a line going off to the distance so it shouldn't matter
                    pos = Util.times(200000., pos);
                }
            }
        }        
        let vec = new Vector3(pos[0], pos[1], pos[2]);
        return vec;
    }
}

export default SepBoxes;