import Util from      "../std/Util.js";
import Constants from "../std/Constants.js";
import Orbit from     "../orbits/Orbit.js";

class Integrator {
    
    constructor(func, x, v, t, dt, dvCallback) {
         this.accel    = func;  // f(x, v, t);
         this.lastX    = x;
         this.lastV    = v;
         this.lastT    = t;
         this.dt       = dt;  // always > 0
         this.deltaX   = 10; // Meters
         this.deltaV   = 1.e-5; // M/s
         this.xe = null;
         this.ve = null;
         this.dvCallback = dvCallback;
    }
    
    stateVector(t) {        
        this.step(t);
        return [this.lastX,this.lastV];
    }
    
    position(t) {
        this.step(t);
        return this.lastX;
    }

    // Do a full requested step to the time t.
    // When the step is finished the lastX and lastV object parameters
    // will have the current position.
    step(t) {
        let dt     = t - this.lastT;
        let absDT  = Math.abs(dt);
        
        if (absDT == 0) {
            return;
        }
                    
        // Loop until we get to the time requested.
        // this.lastT is the where the integrator last stopped.
        do {
            let tLeft = t - this.lastT;
            let tc    = t;
            if (Math.abs(tLeft) > absDT) {
                // Integrate only partway.
                if (dt < 0) {
                    tc = this.lastT - this.dt;
                } else {
                    tc = this.lastT + this.dt;
                }
            }
            this.substep(tc);  // Note that we may not integrate as far as expected.
        } while (this.lastT != t);
    }
    
    // If time delta is too large, we may take multiple smaller steps.
    // But we'll only take one substep this call no matter what.
    // It's up to the calling routine to check that we get as far
    // as it wants.
    substep(t) {
        let cnt = 0;
        
        // rk5a returns true if it succeeds in the integration.
        // When it fails it will specify a smaller delta; 
        while (!this.rk5a(t)  && cnt < 5) {
            t = this.lastT + this.dt;
            cnt += 1;
        };
        if (this.dvCallback) {
            this.dvCallback(this.dt);
        }
        if (cnt > 2) {
            Util.log("Substep count at:"+cnt+ " for time "+t+" "+this.dt);
        }
        // Return the value we got to...
        return this.lastT;
    }
           
    rk5a(t) {
        // Adapted from standard adaptive Runga Kutta, but specialized to
        // acceleration of an object under some external force where the
        // velocity is both an output parameter and a derivative of the position.
        // Uses the this.accel.a method to calculate the instanteous acceleration
        // as a function (potentially) of position, velocity and time 
        // On exit the position, velocity and time of the payload will be updated.
        // An estimate of the error in the integration will be found in the
        // this.xe and this.ve arrays.
        
        let x  = this.lastX;
        let v  = this.lastV;
        let t0 = this.lastT;
        
        // Get the acceleration at the start of the step.  Could probably
        // be a little more efficient by using a value from the previous interation.
        let ac  = this.accel.a(x,v,t0);
                         
        let dt = t-t0;
        
        // These are the standard coefficients of the Cash-Karp Runga-Kutta method.
        const a2=0.2, a3=0.3, a4=0.6,  a5=1.0,  a6=0.875;
        
        const b21=0.2;
        const b31=3.0/40.0,        b32=9.0/40.0;
        const b41=0.3,             b42=-0.9,         b43=1.2;      
        const b51=-11.0/54.0,      b52=2.5,          b53 = -70.0/27.0, b54=35.0/27.0;        
        const b61=1631.0/55296.0,  b62=175.0/512.0,  b63=575.0/13824.0,
              b64=44275.0/110592.0,b65=253.0/4096.0;
              
        const c1=37.0/378.0, c3=250.0/621.0, c4=125.0/594.0, c6=512.0/1771.0;
        const dc5 = -277.00/14336.0;
        const dc1 = c1-2825.0/27648.0,  dc3=c3-18575.0/48384.0;
        const dc4 = c4-13525.0/55296.0, dc6=c6-0.25;
        
        // First step.  
        let x1 = [...x];
        let v1 = [...v];
        // We copy the input x and v values since we're going
        // to add a delta to them.
        
        // Step 1
        for (let i=0; i < 3; i += 1) {
            x1[i] += b21*dt*v[i];
            v1[i] += b21*dt*ac[i];
        }        
        
        let x2 = [...x];
        let v2 = [...v];
        // Step 2
        let ac2 = this.accel.a(x1, v1, t0 + a2*dt); 
        // At this point we could get rid of the x1 values
        // or reuse it, but it's seems cleaner to treat
        // it like the velocity which we will need throughout.
        
        for (let i=0; i<3; i+= 1) {
            x2[i] += dt*(b31*v[i]+b32*v1[i]);
            v2[i] += dt*(b31*ac[i]+b32*ac2[i]);
        }
        
        // Step 3
        let ac3 = this.accel.a(x2, v2, t0+a3*dt);
        let x3 = [...x];
        let v3 = [...v];
        for (let i=0; i<3; i += 1) {
            x3[i] += dt*(b41*v[i] + b42*v1[i] + b43*v2[i]);
            v3[i] += dt*(b41*ac[i] + b42*ac2[i] + b43*ac3[i]);
        }
        
        // Step 4
        let ac4 = this.accel.a(x3, v3, t0+a4*dt);
        let x4 = [...x];
        let v4 = [...v];
        for (let i=0; i<3; i += 1) {
            x4[i] += dt*(b51*v[i] + b52*v1[i] + b53*v2[i] + b54*v3[i]);
            v4[i] += dt*(b51*ac[i] + b52*ac2[i] + b53*ac3[i] + b54*ac4[i]);
        }
        
        // Step 5
        let ac5 = this.accel.a(x4, v4, t0+a5*dt);
        let x5 = [...x];
        let v5 = [...v];
        for (let i=0; i<3; i += 1) {
            x5[i] += dt*(b61*v[i] + b62*v1[i] + b63*v2[i] + b64*v3[i] + b65*v4[i]);
            v5[i] += dt*(b61*ac[i] + b62*ac2[i] + b63*ac3[i] + b64*ac4[i] + b65*ac5[i]);
        }
        // Step 6
        let ac6 = this.accel.a(x5, v5, t0+a6*dt);
        let x6 = [...x];
        let v6 = [...v];
        for (let i=0; i<3; i += 1) {
            x6[i] += dt*(c1*v[i]  + c3*v2[i]  + c4*v3[i]  + c6*v5[i]);
            v6[i] += dt*(c1*ac[i] + c3*ac3[i] + c4*ac4[i] + c6*ac6[i]);
        }
        let xe   = [];
        let ve   = [];
        let xerr = 0;
        let verr = 0;
        for (let i=0; i<3; i += 1) {
            xe[i] = dt*(dc1*v[i]+ dc3*v2[i]+ dc4*v3[i] + dc5*v4[i]+ dc6*v5[i]);
            xerr += xe[i]*xe[i];
            ve[i] = dt*(dc1*ac[i]+ dc3*ac3[i]+ dc4*ac4[i] + dc5*ac5[i]+ dc6*ac6[i]);
            verr += ve[i]*ve[i];
        }
        
        let xrat = xerr / (this.deltaX * this.deltaX);
        let vrat = verr / (this.deltaV * this.deltaV);
        let mrat = Math.sqrt(Math.max(xrat,vrat));
        
        // Is the step size too big.
        if (mrat > 1) {
            let chng = Math.ceil(Math.pow(1.2*mrat, 0.25));  
            this.dt  = dt/chng;
            return false;  // Do not update payload
        } else if (mrat > 0 && mrat < 0.01 && this.adjusted) {
            let chng = Math.pow(1/mrat, 0.2);  // Can use larger stepsize.
            this.dt *= chng;
            Util.log("Larger step by factor:"+chng, 1/Math.sqrt(mrat));
        }
        this.lastX = x6;
        this.lastV = v6;
        this.lastT = t;
        this.xe    = xe;
        this.ve    = ve;
        return true;
    }
}

export default Integrator;      