import Rocket from "../components/Rocket.js";
import Constants from "../std/Constants.js";
import Util from "../std/Util.js";
import {Euler} from "three";
class TravellingRocket {
    
    constructor(args) {
        this.traveller = false;
        
        if (args.name) {
            this.name = args.name;            
        } else {
            TravellingRocket.count += 1;
            this.name = "Rocket_"+TravellingRocket.count;
        }
        if (!args.trajControl) {
            throw new Error("Missing trajectory argument to Travelling Rocket");            
        }
        this.trajControl = args.trajControl;
    }
        
    getTraveller() {
        let here = this;
        if (this.traveller) {
            return this.traveller;
        }        
        let myRocket = new Rocket({name: this.name, height: 0.15});
        myRocket.meta.orbit = this.trajControl.traj;
        let count = 0;

        let update = function(t) {
            let ht = here.trajControl;
            ht.trajUpdate(t)
            let state = ht.lastState;
            let r,v;
            let anim = ht.anim;
            let thrusting = (ht.rocketControl && ht.rocketControl.thrusting) || ht.thrusting;
            
            let att;
            if (thrusting  && ht.lastThrust) {
                att = ht.lastThrust;
            } else if (ht.rocketControl) {
                // Get correct orientation.
                let v3 = here.trajControl.rocketControl.vAbs;
                att = [v3.x, v3.y, v3.z];
            } else {
                att = state[1];
            }
            
            let x = Util.diff(state[0], anim.offset());
            
            // Change from ecliptic to whatever the local system is.
            let trns = anim.orientation();
            
            att = trns.vTransform(att);
            let xx = trns.vTransform(x);
                
            
            // Point in direction of acceleration or velocity.
            let vnorm = Util.unit(att);
            let b = Math.asin(vnorm[2]);
            let l = Math.atan2(vnorm[1], vnorm[0]);
            myRocket.Mfire.visible = thrusting;
            
            let scale = anim.sceneScale;
            
            
            let pos = Util.times(1/scale, xx);
            myRocket.position.set(pos[0], pos[1], pos[2]);
//            if (count % 100 == 0) {
//                console.log("Looking at scaled position:", Util.mag(pos), pos);
//            }
            
            count += 1;
            
            // Remember that the arguments to the Euler constructor
            // are in XYZ order, not the order of rotation.
            myRocket.setRotationFromEuler(new Euler(Math.PI/2-b, 0, Math.PI/2+l, "ZYX"));            
        }
        this.traveller = 
           {name: name, 
            component: myRocket, 
            render: update};
        return this.traveller;            
    }
}

export default TravellingRocket;
