import Util from      "../std/Util.js";
import Constants from "../std/Constants.js";
import {Matrix4, Euler} from "three";

class VThrust {
    
    constructor(mag, camera, x, y) {
        this.mag = mag; 
        
        if (camera == null) {
            this.cam    = null;
            this.velvec = null;
        } else {
            let cp      = camera.position;
            this.cam    = Util.times(Constants.AU, [cp.x, cp.y, cp.z]);
            this.velvec = Util.vec3([0,0,mag]);
            let rot     = new Euler(x-Math.PI/2, y, 0, "XYZ");
            this.velvec.applyEuler(rot);
            this.count  = 0;
        }
    }
    
    a(x,v,t) {
        let acc;
        if (this.cam == null) {
            let unit = Util.unit(v);
            acc = Util.times(this.mag, unit);
        } else { 
            // Unit vectors of the frame in which the instantaneous thrust is defined.
            let units = Util.viewedCoordFrame(this.cam, x, v);
            let mat = new Matrix4();
            mat.makeBasis(Util.vec3(units[0]),Util.vec3(units[1]),Util.vec3(units[2]) );
            let vc = this.velvec.clone();
            vc.applyMatrix4(mat);
            acc = [vc.x, vc.y, vc.z];
            this.count += 1;
        }
        return acc;
    }
}

export default VThrust;

