import {Component} from 'react';
import Constants   from "../std/Constants.js";
import Util from "../std/Util.js";
import Dump from "../std/Dump.js";
import ActionHolder  from "../elems/ActionHolder.js";
import TimeFormatter from "../elems/TimeFormatter.js";

class UserControls extends Component {
    constructor(props) {
        super(props);
        this.scales = [
            {scale: Math.log10(1), symb:"m"},
            {scale: Math.log10(Constants.AU), symb:"au"},
            {scale: Math.log10(9.461e15), symb:"ly"},
            {scale: Math.log10(Constants.PC), symb: "pc"},
            {scale: Math.log10(Constants.PC*1000000), symb:"Mpc"}
        ];
        this.scaleIndex = 0;
        this.scaleElem  = null;
        this.animation = null;
        this.lastScale = null;
        this.state = {
            showOptions: false,
            scaleValue: 1,
            scaleIndex: 0,
            time: 0,
            delta: 0,
            timeFormat: 0,
            running: true,
            quickHTML: null
        };
        this.timeFormatter = new TimeFormatter();
    }
    
    setAnimation(anim) {
        this.animation = anim;
        this.setState({running: !anim.paused});
        if (this.holder) {
            this.holder.setAnimation(anim);
        }
    }
    
    timeClick(e) {
        if (!this.animation) {
            return;
        }
        const cx = e.clientX;
        const cy = e.clientY;
        const targ = e.currentTarget;
        const rect = targ.getBoundingClientRect();
        const pos = (e.clientX - rect.left ) / ( rect.right - rect.left );
        var ind = 0;
        if (pos > 0.2 && pos <= 0.45) {
            ind = 1;
        } else if (pos > 0.45 && pos <= 0.67) {
            ind = 2;
        } else if (pos > 0.67) {
            ind = 3;
        }
        // 0 = reverse, 1 = pause/restart, 2 = slower, 3 = faster
        // Slower and faster are disabled if we are stopped.
        if (this.state.running) {
            switch(ind) {
                case 0: { this.animation.reverse(); break}
                case 1: { this.animation.pauseStart(); 
                          this.setState({running: !this.state.running});
                          break}
                case 2: { this.animation.speed(false); break}
                case 3: { this.animation.speed(true); break}
            }
        } else {
            if (ind == 0) {
                this.animation.pauseStart();
                this.animation.reverse();
                this.setState({running: !this.state.running});
            } else if (ind == 1) {
                this.animation.pauseStart();
                this.setState({running: !this.state.running});
            }
        }
    }
    scaleClick(arg) {
        let scaleIndex = this.state.scaleIndex + 1;
        scaleIndex %= this.scales.length;
        this.setState({scaleIndex: scaleIndex});
    }
    
    setScale(logmeters) {
        this.setState({scaleValue: logmeters});
        this.lastScale = logmeters;
    }
    
    renderScale() {
        let scale = this.scales[this.state.scaleIndex];
        let logmeters = this.state.scaleValue;
        let logscl = logmeters - scale.scale;
        
        let val;
        if (logscl >= 3 || logscl < -2) {
            return <span>10<sup>{logscl.toFixed(2)}</sup>{scale.symb}</span>;
        } else if (logscl >= 0) {
            val = Math.pow(10, logscl).toPrecision(3)+" ";
               
        } else if (logscl >= -1) {
            val = Math.pow(10, logscl).toPrecision(2)+" ";
        } else if (logscl >= -2) {
            // get rid of leading 0.
            val = Math.pow(10, logscl).toPrecision(2).substr(1)+" ";
        }
        return <span>{val + scale.symb}</span>;
    }
    
    optionClick() {
        this.animation.setQuickHTML(null);
        let newState = !this.state.showOptions;
        this.setState({showOptions: newState});
        if (this.holder) {
            this.holder.setState({optState: newState, content: null});
        }
    }
    
    
    render() {
        const here = this;
        var img = this.state.running? 'images/program/runningT.png': 'images/program/stoppedT.png';
        // The userSelect attributes are added to suppress the selection
        // of these elements when we switch animations...
        // There should be a better way.
        return (
          <div className='glasspane'>
           <table style={
           {'userSelect':'none'}}><tbody><tr><td>
              <img onClick={here.optionClick.bind(here)} height='38' src='images/program/dotsT.png' />   
              </td><td valign="middle">
              <div onClick={here.scaleClick.bind(here)} 
                   style={{'textAlign':'center'}}>Scale:
                <br/>
                <span>{this.renderScale()}</span></div></td><td>
              <img style={{userSelect:'none'}}
              onClick={here.timeClick.bind(here)} 
              height='38' width='86' id='speedButtons' src={img}/>
              </td></tr></tbody></table>
            <div onClick={here.updateFormatter.bind(here)}>
               <strong style={{'userSelect':'none'}}>Time: </strong>
               <span>{this.renderTime()}</span>
            </div>
            <ActionHolder anim={this.anim} controls={this} optState={this.state.showOptions} /> 
            <div dangerouslySetInnerHTML={{__html: this.state.quickHTML}} />
          </div>
        );
    }
    
    updateFormatter() {
        this.timeFormatter.updFormat();
        this.setState({timeFormat: this.timeFormatter.format});
    }
    
    timeUpdate(time, delta) {
        this.setState({time: time, delta: delta});
    }
        
    renderTime() {
       return this.timeFormatter.doFormat(this.state.time, this.state.delta);
    }
    
    setQuickHTML(html) {
        this.setState({quickHTML: html});
    }
}

export default UserControls;
