import Animation from "../elems/Animation.js";
import Util from      "../std/Util.js";
import Constants from "../std/Constants.js";
import Galaxies from  "../animations/Galaxies.js";
import Coords from    "../std/Coords.js";

import {Scene, Vector3, MeshBasicMaterial, 
        SphereGeometry, CylinderGeometry, 
        Quaternion, 
        InstancedMesh, Euler, Matrix4, Color} from "three";

class Clusters extends Animation {
    constructor(params) {
        super(params);
        
        this.delta  = Constants.CENTURY/100;
        this.paused = true;
        this.time = 0;
        this.name = "Abell Clusters";          
        this.longClickTime = 1500;  // A little shorter since
                                    // we do less clicking here.

        this.scene       = new Scene();
        this.sceneScale  = 1000000*Constants.PC;
        this.textShowing = true;
        this.centerBody  = "Local Group";
        
        this.cameraPos = new Vector3(0,0,100);
        this.cameraRot = new Euler(0,0,0,"XYZ");
        this.baseAnimation = "Nearby Galaxies";
        this.sceneCoord = "Ga";
        this.minScale = 23.8;
        this.maxScale = 25.5;
        this.biggest  = true;
        
        const here = this; 
        this.loadClusters(this.settings, this.finishConstruction.bind(this));
      
    }
    
    getAnimationFrame() {
        return Coords.getCoords("galec");
    }
    
    finishConstruction(data) {
        
        const material    = new MeshBasicMaterial({wireframe: false});
        const baseCluster = new SphereGeometry(1, 20, 20);
        this.clusters     = data;
                
        
        const theClusters = new InstancedMesh(baseCluster, material, data.length);
        let colors = {};
        colors.I = 0xFF0000;
        colors["I-II"] = 0xFFAAAA;
        colors.II = 0xaaFFaa;
        colors["II-III"] = 0xaaaaFF;
        colors.III = 0x00aaFF;
            
        let size   = [1.5, 1.7, 2, 2.4, 2.9, 3.5, 4, 4];
        for (let i=0; i<data.length; i += 1) {
            
            let clust = data[i];
            let scale = 0.5;
            if (clust.rich >= 0) {
                if (clust.rich > 6 ) {
                    scale = 4;
                } else {
                    scale = size[clust.rich];
                }                
            }
            let matc = new Matrix4().makeScale(scale, scale, scale);

            matc.setPosition(clust.x,clust.y,clust.z);            
            theClusters.setMatrixAt(i, matc);
            if (clust.name == "Local Group") {
                theClusters.setColorAt(i, new Color(0xFFFF00));                
            } else {
                if (colors[clust.bm]) {
                    theClusters.setColorAt(i, new Color(colors[clust.bm]));
                } else {
             
                    
                    theClusters.setColorAt(i, new Color(0x999999));
                }
            }
            
            this.bodyCount += 1;
        }
        this.addElement("clusters", {name: "clusters", component: theClusters,
            render: 
                function(time) {
                }});
        theClusters.getIndexedMeta = 
          function(index) {
            let cluster = this.clusters[index];
            let meta = {...cluster, type:"cluster"};
            return meta;
          }.bind(this);
        this.addTimeDisplay();
        this.finish();        
    }
    
    loadClusters(settings, callback) {
        let xhr  = new XMLHttpRequest();
        let file = "data/clusters.json";
        xhr.open('GET', file, true);
        xhr.onload  = function() {
            let galax = xhr.responseText;
            let json = JSON.parse(galax);
            Util.log("Loading: "+json.length+" Abell Clusters.");
            callback(json);
        };
        xhr.onerror = function() {
            Util.log("Called error function when loading clusters!");
        }
    
        xhr.send(null);
    }
    
    find(name, callback) {
        this.frame.finder.getLocation(name, this.clusters, "cluster", callback);
    }
    
    doLongClick(e) {
        const matched = this.frame.raycast(e);
        
        for (let i=0; i<matched.length; i += 1) {
            let entry = matched[i];
            let name = entry.meta.name;
            if (entry.meta.name == "Local Group") {                
                this.setAnim("Nearby Galaxies");
                return;
            }
        }
        super.doLongClick(e);
    }
    
    find(name, callback) {
        name = name.replace(/\*/g, ".*");
        let match = new RegExp("^"+name+"$", "i");
        let bodies = [];
        for (var i=0; i<this.clusters.length; i += 1) {
            const body = this.clusters[i];           
            if (match.test(body.name)) {
                let meta = {...body, type: "cluster"};
                bodies.push({meta: meta, distance: 0});
            }            
        }
        callback(bodies);
    }
}

export default Clusters;

