import {EventDispatcher, MOUSE, Vector3, Vector2, Quaternion, Euler} from "three";
// This class was adapted from examples/controls/TrackballControls, stripping
// out the vast majority of the functionality but keeping the event handling of the
// drag events to control of the orientation of an object in the scene rather than the
// position of the camera composing the scene.  Lot's of stuff was stripped out
// but there is likely some unused code remaining and there may be bugs introduced
// by the wholesale chopping...
//  
const _changeEvent = {
    type: 'change'
};
const _startEvent = {
    type: 'start'
};
const _endEvent = {
    type: 'end'
};


// Control the orientation of an object through drag events within a given
// DOM element.
class Rotater extends EventDispatcher {

    // object:     The object you want to rotate in response to mouse drags
    // domElement: The DOM element where the drag events are to take place.
    // In principle, the object need not be displayed within the DOM element,
    // but we haven't tried that.    
    constructor(object, domElement) {

        super();
        const scope = this;
        const STATE = {
            NONE: -1,
            ROTATE: 0,
            TOUCH_ROTATE: 3
        };
        this.object = object;
        this.domElement = domElement;
        this.domElement.style.touchAction = 'none'; // disable touch scroll
        // API

        this.enabled = true;
        this.screen = {
            left: 0,
            top: 0,
            width: 0,
            height: 0
        };
        this.rotateSpeed = 1.0;
        this.mouseButtons = {
            LEFT: MOUSE.ROTATE,
        }; // internals

        const EPS = 0.000001;
        let   _state = STATE.NONE;

        const _eye = new Vector3(),
                _movePrev = new Vector2(),
                _moveCurr = new Vector2(),
                _pointers = [],
                _pointerPositions = {}; // for reset


        this.handleResize = function () {

            const box = scope.domElement.getBoundingClientRect(); // adjustments come from similar code in the jquery offset() function

            const d = scope.domElement.ownerDocument.documentElement;
            scope.screen.left = box.left + window.pageXOffset - d.clientLeft;
            scope.screen.top = box.top + window.pageYOffset - d.clientTop;
            scope.screen.width = box.width;
            scope.screen.height = box.height;

        };

        const getMouseOnScreen = function () {

            const vector = new Vector2();
            return function getMouseOnScreen(pageX, pageY) {
                vector.set((pageX - scope.screen.left) / scope.screen.width, (pageY - scope.screen.top) / scope.screen.height);
                return vector;
            };
        }();

        const getMouseOnCircle = function () {
            const vector = new Vector2();
            return function getMouseOnCircle(pageX, pageY) {
                vector.set((pageX - scope.screen.width * 0.5 - scope.screen.left) / (scope.screen.width * 0.5), (scope.screen.height + 2 * (scope.screen.top - pageY)) / scope.screen.width // screen.width intentional
                        );
                return vector;
            };
        }();

        this.rotateObject = function () {
            const moveDirection = new Vector3();
            return function rotateObject() {
                moveDirection.set(_moveCurr.x - _movePrev.x, _moveCurr.y - _movePrev.y, 0);
                let angle = moveDirection.length();
                if (angle) {
                    let dx = (_moveCurr.x - _movePrev.x);
                    let dy = (_moveCurr.y - _movePrev.y);

                    let r = scope.object.rotation;
                    r.x = (r.x - dy);
                    r.y = (r.y - dx);
                    r.x %= 2 * Math.PI;
                    r.y %= 2 * Math.PI;
                }
                _movePrev.copy(_moveCurr);
            };
        }();

        this.update = function () {
            scope.rotateObject();
        };


        function onPointerDown(event) {

            if (scope.enabled === false)
                return;

            if (_pointers.length === 0) {
                scope.domElement.setPointerCapture(event.pointerId);
                scope.domElement.addEventListener('pointermove', onPointerMove);
                scope.domElement.addEventListener('pointerup', onPointerUp);

            }
            addPointer(event);

            if (event.pointerType === 'touch') {
                onTouchStart(event);

            } else {
                onMouseDown(event);
            }
        }

        function onPointerMove(event) {

            if (scope.enabled === false)
                return;

            if (event.pointerType === 'touch') {
                onTouchMove(event);
            } else {
                onMouseMove(event);
            }
        }

        function onPointerUp(event) {
            if (scope.enabled === false)
                return;

            if (event.pointerType === 'touch') {
                onTouchEnd(event);
            } else {
                onMouseUp();
            }

            removePointer(event);

            if (_pointers.length === 0) {
                scope.domElement.releasePointerCapture(event.pointerId);
                scope.domElement.removeEventListener('pointermove', onPointerMove);
                scope.domElement.removeEventListener('pointerup', onPointerUp);
            }
        }

        function onPointerCancel(event) {
            removePointer(event);
        }


        function onMouseDown(event) {

            if (_state === STATE.NONE) {
                _state = STATE.ROTATE;
            }

            _moveCurr.copy(getMouseOnCircle(event.pageX, event.pageY));
            _movePrev.copy(_moveCurr);

            scope.dispatchEvent(_startEvent);
        }

        function onMouseMove(event) {
            _movePrev.copy(_moveCurr);
            _moveCurr.copy(getMouseOnCircle(event.pageX, event.pageY));
        }

        function onMouseUp() {
            _state = STATE.NONE;
            scope.dispatchEvent(_endEvent);
        }

        function onTouchStart(event) {
            trackPointer(event);
            switch (_pointers.length) {
                case 1:
                    _state = STATE.TOUCH_ROTATE;
                    _moveCurr.copy(getMouseOnCircle(_pointers[ 0 ].pageX, _pointers[ 0 ].pageY));
                    _movePrev.copy(_moveCurr);
                    break;

                default:
                    break;
            }

            scope.dispatchEvent(_startEvent);
        }

        function onTouchMove(event) {

            trackPointer(event);
            switch (_pointers.length) {

                case 1:
                    _movePrev.copy(_moveCurr);
                    _moveCurr.copy(getMouseOnCircle(event.pageX, event.pageY));
                    break;

                default:
                    break;
            }
        }

        function onTouchEnd(event) {

            switch (_pointers.length) {

                case 0:
                    _state = STATE.NONE;
                    break;

                case 1:
                    _state = STATE.TOUCH_ROTATE;
                    _moveCurr.copy(getMouseOnCircle(event.pageX, event.pageY));
                    _movePrev.copy(_moveCurr);
                    break;

                case 2:
                    break;

            }

            scope.dispatchEvent(_endEvent);
        }

        function contextmenu(event) {

            if (scope.enabled === false)
                return;
            event.preventDefault();
        }

        function addPointer(event) {
            _pointers.push(event);
        }

        function removePointer(event) {
            delete _pointerPositions[ event.pointerId ];

            for (let i = 0; i < _pointers.length; i++) {
                if (_pointers[ i ].pointerId == event.pointerId) {
                    _pointers.splice(i, 1);
                    return;
                }
            }
        }

        function trackPointer(event) {

            let position = _pointerPositions[ event.pointerId ];

            if (position === undefined) {
                position = new Vector2();
                _pointerPositions[ event.pointerId ] = position;
            }

            position.set(event.pageX, event.pageY);
        }

        this.dispose = function () {
            scope.domElement.removeEventListener('contextmenu', contextmenu);
            scope.domElement.removeEventListener('pointerdown', onPointerDown);
            scope.domElement.removeEventListener('pointercancel', onPointerCancel);
            scope.domElement.removeEventListener('pointermove', onPointerMove);
            scope.domElement.removeEventListener('pointerup', onPointerUp);

        };

        this.domElement.addEventListener('contextmenu', contextmenu);
        this.domElement.addEventListener('pointerdown', onPointerDown);
        this.domElement.addEventListener('pointercancel', onPointerCancel);
        this.handleResize(); // force an update at start
        this.update();
    }
}

export default Rotater;