import Constants from "../std/Constants.js";
import Util from "../std/Util.js";

class TimeFormatter {
    
    constructor() {
        this.format  = 0;
        this.nFormat = 3;
        this.saveTime  = null;
        this.saveDelta = null;
    }
    
    doFormat(time, delta) {
        switch (this.format) {
            case 0: return this.dateFormat(time,delta);
            case 1: return this.yearFormat(time,delta);
            case 2: return this.jdFormat(time,delta);
            default: return this.noDate(time,delta)
        }
        this.saveTime  = time;
        this.saveDelta = delta;
    }
    
    updFormat() {
        if (this.format >= 0 && this.format < this.nFormat) {
            this.format += 1;
            this.format %= this.nFormat;
        }
    }
    
    // In JD
    jdFormat(time, delta) {
        let scl = -Math.log10(delta/86400);
        let dig = Math.ceil(scl);
        if (dig < 0) {
            dig = 0;
        } else if (dig > 6) {
            dig = 6;
        }
        let jd = (time/86400 + Constants.JD0);
        return jd.toFixed(dig) + " JD";
    }
    
    lastTime() {
        return this.doFormat(this.saveTime, this.saveDelta);
    }
    
    dateFormat(time, delta) {
        // Format a time given the time in seconds since 2000 and the current
        // delta to time.
        let len =   5;  // Get rid of last five characters in time 
        let add = 0.5;  // Add 0.5 seconds to round time.
        let suffix = "";
        let dabs = Math.abs(delta);
        if (dabs != 0) {
            if (dabs <= 0.005) {
                len = 1;
                add = 0.0005;
            } else if (dabs < 0.05) {
                len = 2;
                add = 0.005;
            } else if (dabs < 0.5) {
                len = 3;
                add = 0.05;
            } else if (dabs < 60) {
                len = 5;
                add = 0.5;
            } else if (dabs < 3600) {
                len = 8;
                add = 30;
            } else if (dabs < 86400) {
                len = 11;
                add = 1800;
                suffix = "h";
            } else if (dabs < Constants.CENTURY/100) {
                len = 14;
                add = 43200;
            } else {
                len = 20;
                add = Constants.CENTURY/200;
            } 
        }
        if (len == 20) { // Just the year.
            let yr = Math.floor(2000+(time+add)*100/Constants.CENTURY);
            if (yr < 1) {
                yr = Math.abs(yr)+2;
                return yr + " BC";                
            } else { 
                return yr + " AD";
            }
        }
        const millis = ((time+add) / 86400 + Constants.JD0 - 2440587.5) * 86400000;
        const dateLocal = new Date(millis).toISOString();                       
        return dateLocal.substring(0, dateLocal.length-len)+suffix;
    }
    
    yearFormat(time, delta) {
        // Allow for fine grained years.
        let scale = 0;
        if (delta != 0) {
            scale = Math.floor(Math.log10(Math.abs(delta)/Constants.CENTURY)) + 2; 
        }
        if (scale > 0) {
            scale = 0;
        } else if (scale < -6) {
            scale = -6;
        }
        if (scale < 0) {
            scale = -scale;
        }
        let yr = 100*time/Constants.CENTURY + 2000;
        if (yr < 1) {
            // No year 0.  Note that 1.25 AD is in the end of Spring, but 1.25 BC is the end of Autumn.
            // 1.000 BC is the instant before 1.000 AD
            yr -= 2;    // So 0.75 -> -1.25    
        }
        let yrStr = Math.abs(yr).toFixed(scale);
        if (yr > 0) {
            return yrStr +" AD";
            
        } else {
            return yrStr + " BC";
        }
    }
    
    noDate(time,delta) {
        return '';
    }
}
 export default TimeFormatter;
