import Util         from "../std/Util.js";
import Constants         from "../std/Constants.js";
import ChainedOrbit from "../orbits/ChainedOrbit.js";

import {ConeGeometry,Group, MeshBasicMaterial,Mesh, Color, Euler} from "three";

function Missions(missions,start) {
    let results = [];
    for (let miss in missions) {
        let chain = ChainedOrbit.createChain(miss, missions[miss]);
        let time = chain.startTime;
        const geometry = new ConeGeometry(.015, 0.06, 8);
        const material = new MeshBasicMaterial({wireframe: false, color: new Color(missions[miss].color)});
        // We put the actual object inside a container so that we can
        // preserve the twist to exchange the Y andZ axes without worrying about addding it to the 
        // velocity orientation explicitly.
        const xcone = new Mesh(geometry, material);
        xcone.rotateX(Math.PI/2);
        const cone  = new Group();
        cone.add(xcone);
        
        const p        = chain.position(start);
        let count = 0;
        xcone.meta = {...missions[miss],
            name: miss,
            type: "mission",
            orbit: chain
        }
        cone.meta = xcone.meta;
        // Missions may be hidden at the center of the Sun.
        // So we aren't supposed to find them in the animation if their
        // radius is very small (probably could just check for = 0).
        let animHidden = 
           function() {
               return this.position.length() < 1.e-10;
           };
        animHidden = animHidden.bind(cone);
        xcone.animHidden = animHidden;
        cone.animHidden = animHidden;
        const ret =             
            {component: cone, name: miss, chain: chain,
        
             render: function(time) {
              // Update planet positions.
                var s = chain.stateVector(time);
                let r = s[0];
                r = Util.times(1/Constants.AU, r);
                let v = s[1];
                
                // Using ret.component vs just sphere
                // allows us to replace the component externally.
                ret.component.position.set(r[0], r[1], r[2]);
                if (r[0] != 0) {
                    v = Util.times(1./Util.mag(v), v);
                    let l = Math.atan2(v[1], v[0]);
                    let b = Math.asin(v[2]);
                    ret.component.setRotationFromEuler(new Euler(Math.PI/2, l+Math.PI/2, b, "XZY"));
                }
      }};       
        results.push(ret);

    } 
    return results;
}

export default Missions;