import {Component} from "react";

// This class allows one to press a button
// for a period of time.  The down callback
// is called when the button is first pressed
// and the up callback is called when the
// mouse is released or leaves the button.
class PushButton extends Component {
    
    constructor(args) {
        super(args);
    
        this.label = args.label;
        if (this.label == null) {
            this.label = "Push";
        }
        this.state = {isDown: false};       
    }
    render() {    
        return (
            <button
                disabled={this.props.disabled}
                onMouseDown={this.props.down}
                onTouchStart={this.props.down}
                onMouseUp={this.props.up}
                onMouseOut={this.props.up}
                onTouchEnd={this.props.up}
            > {this.label}</button>
        );
    }
}

export default PushButton;

