/** An animation is a scene comprised of elements
 * whose state varies with time. This class addresses non-scene specific management
 * of the animation. 
 */

import {createElement} from "react";
import Dump from "../std/Dump.js";

import {Scene, Vector3, Euler} from "three";
import {Mesh, TextureLoader, Color, MeshPhongMaterial} from "three";
import {SphereGeometry, 
        MeshLambertMaterial, MeshBasicMaterial, DoubleSide} from "three";
    
import {RingGeometry} from "../three/RingGeometry.js";  // Local Override to change UV mapping

import Util        from "../std/Util.js";
import Settings    from "../std/Settings.js";
import Constants   from "../std/Constants.js";
import Coords      from "../std/Coords.js";
import ObjList     from "../elems/ObjList.js";
import ObjList2    from "../elems/ObjList2.js";
import SolarPlanets      from "../solarsystem/SolarPlanets.js";
import TravellingRocket  from "../elems/TravellingRocket.js";

class Animation {
    
    // Set defaults and create basic Three framework
    constructor(inputs) {
        
        // Defaults
        const startup = {
            cameraPos:     new Vector3(0,0,6),      // Initial position of camera
            cameraRot:     new Euler(0,0,0,"XYZ"),  // Initial rotation.
            
            cameraFoV:     75,       // Width of field of view.
            camNear:       0.0001,   // Nearest object we can see
            camFar:        2000.,    // Most distant object we can see
            
            sceneElements: {},       // The elements that comprise the scene.
            
            paused:        false,    // Has the user paused the time updates?
            runOne:        false,    // Run a single animation frame, then stop.
            
            counter:       0,        // The number of renders   
            bodyCount:     0,        // Number of elements being rendered.
            time:          Date.now()/1000 - Constants.Epoch0,  // The current time
            
            segments:      40,        // How many segments when drawing spheres.
            
            delta:         21600,     // The interval between steps (21600 s = 6 hours = 0.25 day)
            scene:         new Scene(), // The scene that holds this animation.
            name:          "Generic", // Place holder name
            quiet:         false,     // Suppress messages about loading/unloading components.
            loading:       false      // Set if something needs to be loaded before display
        };
 
        // Set defaults
        for (var key in startup) {
            this[key] = startup[key];
        }
        
        // Override defaults.
        if (inputs) {
            for (var key in inputs) {
                this[key] = inputs[key];
            }
        }        
                
        this.settings = Settings;
        this.finished = false;        
    }
    
    
    // Things to do after finishing other rendering for animation.
    finish() {
        this.finished = true;
    }


    // Update the content of the animation for the next frame.
    // This does not do the animation, it only does any updates
    // needed to do the rendering.
    update (){
        this.counter += 1;
        
        if (!this.paused) {
            this.time += this.delta;
            this.updateElements();
        }
        
    }
    
    
    
    getCurrentScale() {
        let scale    = this.getBaseScale();
        let center   = this.frame.controls.target;
        let position = this.frame.camera.position;
        let distance = position.distanceTo(center);
        return Math.log10(distance*scale);        
    }
    
    // Update each element that has been added to the animation.
    updateElements() {
        for (var key in this.sceneElements) {
            
            // If the element has a function to be called for
            // each rendering call it with the curren time.
            var rfunc = this.sceneElements[key].render;
            if (rfunc) {
                rfunc(this.time);
            }
        }
    }

    // Add an element to the animation
    // The sceneElement has three components:
    //     name: A name (often the first as the elemName argument)
    //     component: An element to be rendered in the scene
    //         (optional: e.g., the time rendering has no associated three.js component)
    //     render: A function to be called before doing the render.
    //         (optional: e.g., a static component like the Sun may not need any updating.)
    //   
    addElement(elemName, sceneElement, notBody) {
        if (this.sceneElements[elemName]) {
            Util.log("Attempt to duplicate element "+elemName+" in "+this.name);            
            return;
        }
        
        sceneElement.notBody = notBody;
        this.sceneElements[elemName] = sceneElement;
        if (sceneElement.component) {
            sceneElement.component.elementName = elemName;
            this.scene.add(sceneElement.component);
        }
        
        // If adding lots of components, may wish to set quiet flag.
        if (!this.quiet) {
            Util.log("Added element #"+Object.keys(this.sceneElements).length+": "+elemName+" to "+this.name);
        }
        if (!notBody) {
            this.bodyCount += 1;
        }
    }
    
    // Remove an element from the scene (permanently).
    removeElement(elemName) {
        if (!this.sceneElements[elemName]) {
            Util.log("Request to delete non-existent element "+name+" of "+this.name);
            return;
        }
        
        if (!this.sceneElements[elemName].notBody) {
            this.bodyCount -= 1;
        }
        
        this.scene.remove(this.sceneElements[elemName].component);
        delete(this.sceneElements[elemName]);
        if (!this.quiet) {
            Util.log("Removed element "+elemName+" from "+this.name);
        }
        
    }

    // Change the time interval between frames.
    speed(faster) {
        if (faster) {
            this.delta *= Math.sqrt(2);
        } else {
            this.delta /= Math.sqrt(2);
        }
    }
    
    find(input, callback) {
        this.frame.finder.getLocation(input, null, null, callback);
    }
 
    // Reverse the sign of the time intervals.
    reverse() {
        this.delta *= -1;
        
        // Take the occastion to clean up any detritus.
        this.setQuickHTML("");
        
        // Some things may be affected by the change in direction of
        // the time of the simulation.
        let evt = new Event("timereverse");
        this.frame.renderer.domElement.dispatchEvent(evt);
    }
    
    // Pause or restart the animation.
    pauseStart() {
        this.paused      = !this.paused;
    }
    
    // Set the time for the animation (in seconds since 2000)
    setTime(time) {                
        this.time   = time;
        this.updateElements();
    }
 
    // Open the help window.
    openHelp() {
        window.open(this.getHelpURL(), "help");
    }
    
    // Pass on the epochs of various items to help.
    getHelpURL(){
        let url = "help_top.html";
        
        let smb = this.frame.times.smb;
        let sat = this.frame.times.sat;
        if (sat == null) {
            sat = ""; 
        }
        if (smb) {
            url += "?smbEpoch="+smb+"&satEpoch="+sat;
        }
        return url;
    }
    

    // Open the orbit params window.  Note that since
    // this window shares code with the animation window,
    // we actually call the same base URL and distinguish
    // the window with a CGI parameer.
    openParams() {
        window.open("?app=OrbParams", "params");
    }

    // Set the animation to be used to a new animation.
    setAnim(animation) { 
        const anim = this.frame.animFactory.getAnimation(animation, {frame: this.frame});
        this.frame.setAnimation(anim);
    }
    
    getBaseScale() {
        return this.sceneScale;
    }

    // Reset the time given an unparsed input.
    // Different children may have different parseTime methods.
    updateTime(time) {
        var newTime = Util.parseTime(time);
        this.setTime(newTime);
    }
    
    // The default time parser which uses the 
    // funky JavaScript Date.parse function.
    // SHOULD GET RID OF THIS WHEN SURE NOT NEEDED0
    parseTime(time) {
        if (!time || time.length < 4) {
            return;
        }
        if (time.indexOf(":") < 0) {
            // Kludge that fixes a particular browser...
            time += " 00:00";
        }
        var date = Date.parse(time);
        if (date) {
            const newTime = date/1000 - Constants.Epoch0;
            return newTime;
        } else {
            return 0;
        }       
    }

    // Add a virtual widget to be called to update the time displayed
            
    addTimeDisplay() {
        const here = this;
        this.addElement(
                "Timer",
                {name: "tdisp",
                    render: function (time) {
                        here.frame.userControls.timeUpdate(here.time, here.delta);
                    }
                }, true);
    }        
    
    // Handle a long click detected in the window.
    // This default behavior goes to the "baseAnimation"
    // if the user does a longClick in a corner.
    // Children may do more specialized actions
    // in response and use super.doLongClick if
    // none of these seem appropriate.
    doLongClick(e) {
        if (this.baseAnimation) {
            const canvas = this.frame.renderer.domElement;
            var rect = canvas.getBoundingClientRect();
            const x = ( e.clientX - rect.left ) / ( rect.right - rect.left );
            const y = ( e.clientY - rect.top ) / ( rect.bottom - rect.top );
        
            if ( ( (x <= .1 ) || (x >= .9) )  &&
                   (y <= .1 ) || (y >= .9) ) {
                // This covers just 4 x 0.5*.1*.1 or 2% of the surface of the canvas.
                this.setAnim(this.baseAnimation);
            }
        }
    }
    
    // When loading a texture onto a sphere from a cartesian map,
    // three.js renders the y axis of the map onto the y axis of the
    // sphere, while the x and z axes are composed from x axis.
    // This renders the center of the map at the 'north pole', i.e., maximum z.
    // Since we want to use the proper coordinate system for the
    // other system, we need to adjust to initially render
    // the object and to ensure that we rotate along the appropriate axis.

    createSphere(radius, segments, texture, ring, name) {
        var mesh;
        
        const loader  = new TextureLoader();
        let map  = null;
        let bump = null;
        let spec = null;
        let specColor = null;
        if (texture.map) {
            map  = loader.load(texture.map);
        }
        if (texture.bump) {
            bump = loader.load(texture.bump);
        }
        if (texture.spec) {
            spec = loader.load(texture.spec);
            specColor = new Color("grey");
        }

        mesh = new Mesh(
        new SphereGeometry(radius, segments, segments),
          new MeshPhongMaterial({
            map:     map,
            bumpMap: bump,
            bumpScale: 0.005,
            specularMap: spec,
            specular: specColor
          })
        );
        mesh.rotateX(Math.PI/2);  // Move the pole where it should be!
        mesh.receiveShadow = true;
        
        if (ring) {
            let map   = new TextureLoader().load(ring.texture.map);
            let ringG = new RingGeometry(ring.innerRadius, ring.outerRadius, 2*segments);
            const rMat = 
                new MeshPhongMaterial({
                   map:         map,
                   specular:    specColor,
                   side:        DoubleSide
                });
            let ringM = new Mesh(ringG, rMat);
            ringM.position.set(0,0,0);
            ringM.rotateX(Math.PI/2);
            mesh.add(ringM);
            ringM.elementName = name + "Ring";
            ringM.receiveShadow = true;
        }
        return mesh;
    }
    static rrcount = 1;
    
    raycastResult(results) {
        this.setQuickHTML("");
        let list = null;
        // Fix some mysterious caching problem...
        if (Animation.rrcount % 2 == 1) {
            list = createElement(ObjList, {list: results, anim:this, holder:this.frame.userControls.holder});
        } else {
            list = createElement(ObjList2, {list: results, anim:this, holder:this.frame.userControls.holder});
        }
        this.frame.userControls.holder.setState({
            optState: true,
            content:  list
        });
        Animation.rrcount += 1;
    }
    
    setQuickHTML(html) {
        if (html) {
            html = "<hr/>"+html;
        }
        this.frame.userControls.setQuickHTML(html);
    }
    
    // The following methods allow us to embed multiple animations in larger
    // framework.  We situate all animations relative to the Sun in Ecliptic
    // coordinates.
    
    // Returns the offset between the animation center and the Sun.  The offset
    // is given in meters in standard ecliptic coordinates.
    offset() {
        return [0,0,0];
    }
    
    // Returns the rotation matrix around the current center of the animation 
    // to ecliptic coordinates.
    orientation() {
        if (this.coordSystem == null) {
            this.coordSystem = this.getAnimationFrame();
        }
        return this.coordSystem;
    }
    
    // The default frame for an animation is just Ecliptic coordinates, so
    // we just return the unit transformatino.
    getAnimationFrame() {
        let coords = Coords.getCoords("unit");
        return coords;
    }
};

export default Animation;