import Coords from "../std/Coords.js";

class TransformedOrbit {
    
    constructor(orbit) {
        this.orbit = orbit;
        this.trans = null;
    }
    
    addTransform(add) {
        
        // First transformation
        if (this.trans == null) {
            this.trans = add;
            return;
        }
        
        // Add the transform to the existing transform.  
        // Find the pole of the initial system in the final system
        // and vice versa.
        let iPole = [0,0,1];
        let fPole = [0,0,1];
        
        iPole = this.trans.vTransform(iPole);
        iPole = add.vTransform(iPole);
        
        fPole = add.invTransform(fPole);
        fPole = this.trans.invTransform(fPole);
        
        let lonFPole = Math.atan2(fPole[1], fPole[0]);
        let lonIPole = Math.atan2(iPole[1], iPole[0]);
        let theta    = Math.asin(iPole[2]); // Should also equal Math.asin(fPole[2])
        let tx       = Math.asin(fPole[2]);
        this.trans   = Coords.getCoords([lonFPole, theta, lonIPole]);
    }
    
    position(time) {
        return this.transform(this.orbit.position(time));
    }
    
    transform(r) {
        return this.trans.vTransform(r);
    }
    
    orbitXYZ(n, t) { 
        let vec = this.orbit.orbitXYZ(n, t);
        return this.multi(vec);
    }
    
    stateVector(time) {
        // State just returns two vectors, r and v
        return multi(this.orbit.stateVector(time));
    }
    
    multi(vec) {
        if (isArray(vec[0])) {
            for (let i=0; i<vec.length; i += 1) {
                vec[i] = this.transform(vec[i]);
            }
        } else {
            Util.log("Call to multivector transform without multivector input");
        }
        return vec;
    }
}

export default TransformedOrbit;