import Action from "./Action.js";

class echo extends Action {

    constructor(ptr, anim, traj) {
        super(ptr, anim, traj);
    }
    
    execute(reverse) {
        if (this.anim.delta > 0  || reverse) {
            this.anim.setQuickHTML(this.ptr.elem.forward);
        } else {
            if (!this.executePrev(this.ptr, ["echo"]) ) {
                this.anim.setQuickHTML("");
            }
        }
    }
}

export default echo;
    
