/**
 * This class defines the framework in which animations are displayed.
 * We use a single framework for all animations.
 */
import {PerspectiveCamera, WebGLRenderer, Object3D, Raycaster, Vector3} from "three";
import { OrbitControls} from 'three/examples/jsm/controls/OrbitControls';
import { TrackballControls} from 'three/examples/jsm/controls/TrackballControls';

import Util from "../std/Util.js";
import Dump from "../std/Dump.js";
import Constants from "../std/Constants.js";
import GenFinder from "../stars/GenFinder.js";
import Transition from "../animations/Transition.js";


class Frame {
 
    
    // Build the orrery frameword. 
    // The OrreryBuilder will actually add the components to
    // the orrery.
    
    constructor(inputs) {
        const defaults = {            
            camera:       null,  // The camera associated with the frame
            renderer:     null,  // The renderer used
            controls:     null,  // The controls used                                  
            animation:    null,  // The animation being run
            userControls: null,  // User control widget. 
            trajControl:  null   // Controller for dynamic trajectory that can span animations.
        };
        this.scaleDepth = 0;
        
        for (var key in defaults) {
            this[key] = defaults[key];
        }
        
        this.mouseTime    = null;
        this.controlClick = true;
        
        // Override defaults.
        if (inputs) {
            for (var key in inputs) {
                this[key] = inputs[key];
            }
        }
        
        this.startedRun       = false;
        
        this.setupTHREE(this.animDiv);
        
        this.renderer.domElement.addEventListener("longClick",
          this.longClickEvt.bind(this));
          
        this.renderer.domElement.addEventListener("dblclick",
          this.dblClickEvt.bind(this));
                    
        this.finder        = new GenFinder(this);
    }
    
    // Recall that the event with mouse information is
    // in the detail of the longClick event.
    longClickEvt(e) {
        // We want this to be handled by the current animation
        // so use the current doLongClick handler.
        if (this.animation.doLongClick) {
           this.animation.doLongClick(e.detail);
        }
    }
  
        
    dblClickEvt(e) {
        this.animation.doLongClick(e);
    }
    
    setUserControls(controls) {
        this.userControls = controls;
    }

    prepToLeave() {
        this.animation.cameraPos    = this.camera.position.clone();
        this.animation.cameraRot    = this.camera.rotation.clone();
        this.animation.cameraTarget = this.controls.target.clone();
        if (this.trajControl) {
            // Get rid of the marker for the trajectory since it may be deleted
            // before we come back to this animation.
            this.trajControl.delRocket();
        }
    }
    
    prepToEnter(anim) {
        this.camera.position.copy(anim.cameraPos);
        this.camera.rotation.copy(anim.cameraRot);
        if (anim.cameraTarget) {
            this.controls.target.copy(anim.cameraTarget);
        } else {
            this.controls.target = new Vector3(0,0,0);
        }
        this.camera.updateProjectionMatrix();                
        this.userControls.setAnimation(anim);
        
        if (anim.sceneElements.Timer) {
            anim.sceneElements.Timer.render(anim.time);
        }
        
        if (this.trajControl) {
            this.trajControl.anim = this.animation;
            this.trajControl.addRocket();
        }
        
        this.userControls.holder.setState({optState: false,
                                           content: null});
            

    }
        
    /** This function changes the current animation being shown.
     *  Only a single animation is visible at a given time.
     */
    setAnimation(anim) {
        if (this.animation) {
            this.prepToLeave();
            anim.time = this.animation.time;

        } else {
            Util.log("Starting initial animation ",anim.name);
        }
        // Animation changes here!
        this.animation = anim;
        
        this.prepToEnter(anim);
        
        if (!this.startedRun) {
            this.run();
        }
        this.updateScale();
        anim.setQuickHTML("Animation: "+anim.name);
        
    }
    
    // These methods handle information that needs to be transferred between 
    // animations.
    getTransferPackage() {
        let pack = {};
        pack.time = this.animation.time;
        return pack;
    }
    
    processTransferPackage(pack) {
        if (pack) {
            this.animation.time = pack.time;
        }
    }
        
    // Run the animations.  Note that when
    // we change animations, the underlying this.animation will
    // change, but we are still using the same frame and sequence
    // of render calls so we should only call this once.
    run (){
        
        if (this.startedRun) {
            Util.log("Unexpected attempt to start second animation run");
            console.trace();
            return;
        }
        
        this.startedRun = true;
        // Save context for render
        const here   = this;
        this.cnter = 0;
        
        // Doing this using a closure seems to work
        // more reliably than trying to bind the render function to the frame.
        // If I understood JavaScript better might be a better way to do this.
        function doRender() {
            requestAnimationFrame(doRender);
            here.render();
        }
        
        doRender();
    }
       
    // This is the function that does all animations!
    render()  {
        this.cnter += 1;
        

        if (this.animation.loading) {
            Util.log("Loading "+this.animation.name);

        } else {
            this.animation.update();
            this.controls.update();
            if (this.cameraUpdate) {
                this.cameraUpdate.update(this.camera, this.anim.time);
            }
            this.renderer.render(this.animation.scene, this.camera);
        }
    }
    
    mouseTimeDown() {
        if (!this.mouseTime) {
            this.mouseTime = Date.now();
        }
    }
    
    mouseTimeUp(e) {        
        if (!this.mouseTime) {
        } else if (this.animation.longClickTime) {
            let delta  = Date.now() - this.mouseTime;
            if (delta > this.animation.longClickTime) {
                this.renderer.domElement.dispatchEvent(
                   new CustomEvent("longClick", {detail:e})
                );               
            }
        } else {
        }
        this.mouseTime = null;
    }
    
    // Define the THREE.js framework.
    setupTHREE(div) {
        
        var elem;
        if (div) {
            elem = document.getElementById(div);
        }
        if (!elem) {
            elem = document.body;
        }
        
        this.camera   = new PerspectiveCamera( this.cameraFoV, 1, this.camNear, this.camFar );
        this.renderer = new WebGLRenderer({antialias: true});

        this.renderer.setClearColor("#050505");
        
        this.renderer.setSize(this.size[0], this.size[1]);
        this.resize(this.size[0], this.size[1]);
		
        this.raycaster = new Raycaster();
        this.raycaster.params.Line   = { threshold: .08 };
	this.raycaster.params.Points = { threshold: .05 };
        this.raycaster.near = 0;
        this.raycaster.far  = 500;
        
        elem.appendChild(this.renderer.domElement);
        
        const canvas = this.renderer.domElement;
        this.controls = new TrackballControls (this.camera, canvas);
        this.controls.addEventListener("change", this.updateScale.bind(this));
        this.controls.dynamicDampingFactor = 1;
        
        canvas.addEventListener("mouseup",        this.mouseTimeUp.bind(this));
        canvas.addEventListener("mousedown",      this.mouseTimeDown.bind(this));
        canvas.addEventListener("click",          this.clickListener.bind(this));
    }
    
    updateScale() {
        this.scaleDepth += 1;
        
        let scale    = this.animation.getCurrentScale();
        let minScale = this.animation.minScale || 0;
        let maxScale = this.animation.maxScale || 1.e20;
        
        let csave = this.animation.csave;
        if (csave == null) {
            csave = {};
            this.animation.csave = csave;
        }

        // Handle zooms outside of allowed range.
        if (scale > maxScale || scale < minScale  && csave.position) {
            Util.log("Scale issue: ", scale, minScale, maxScale, this.animation.name);
            
            // Revert to previous position
            let camOrig  = this.camera.position.clone();
            let targOrig = this.controls.target.clone(); 
            this.camera.position.copy(csave.position);
            this.camera.rotation.copy(csave.rotation);
            this.controls.target.copy(csave.target);
            if (scale > maxScale  && this.animation.baseAnimation  && !this.animation.biggest) {
                let dist = 1;
                let oldName   = this.animation.name;
                let oldNorm   = this.animation.getBaseScale(); 
                let oldTime   = this.animation.time;
                let oldPaused = this.animation.paused;
                let oldDelta  = this.animation.delta;
                
                this.animation.setAnim(this.animation.baseAnimation, true);
                
                let list = this.finder.getLocalList(oldName,null, null);
                if (list  && list.length == 1) {
                    // Can find our previous center in the new coordinate 
                    // system
                    let delta   = list[0].component.position.clone();
                    let newNorm = this.animation.getBaseScale();
                     
                    // Convert the old values to the new coordinate system                    
                    camOrig.multiplyScalar(oldNorm/newNorm).add(delta);
                    targOrig.multiplyScalar(oldNorm/newNorm).add(delta);
                    
                    // How does the scale compare with the minimum scale or
                    // the next level animation.
                    let xscale = Math.log10(camOrig.distanceTo(targOrig)*newNorm);
                    
                    // If we are too close, then move out in the same direction by
                    // the smallest possible factor.
                    if (xscale < this.animation.minScale) {
                        let xfac = Math.pow(10, this.animation.minScale-xscale + 0.01);
                        scale += (this.animation.minScale-xscale)+0.01;
                        let delta = camOrig.clone();
                        delta.sub(targOrig);
                        delta.multiplyScalar(xfac);
                        delta.add(targOrig);
                        camOrig.copy(delta);
                    }
                    
                    this.controls.target.copy(targOrig);
                    this.camera.position.copy(camOrig);
                    
                    this.animation.time   = oldTime;
                    this.animation.paused = oldPaused;
                    if (this.animation.delta * oldDelta < 0) {
                        // Let the animations proceed at different rates, but they
                        // should go in the same direction.
                        this.animation.delta *= -1;
                    }                                        
                } else {
                    let base = this.animation.getBaseScale();
                    let dist = 1; 
                    if (this.animation.minScale) {
                        let meters = Math.pow(10, this.animation.minScale)*1.01;
                        dist = meters/base;                    
                    }
                    this.camera.position.copy(new Vector3(0,0,dist));
                    this.controls.target.copy(new Vector3(0,0,0));
                    if (this.animation.minScale > scale + 1) {
                        let toAnim = this.animation;
                        Util.log("Creating transition: ", this.animation.name);
                        this.setAnimation(new Transition({frame:this, minSteps: 120, sign: -1, toAnim: toAnim}));
                    }
                }
                this.camera.updateProjectionMatrix();        
            }
            
        } else {
            // Save the curren values in case the next zoom
            // needs to be suppressed.
            csave.position = this.camera.position.clone();
            csave.rotation = this.camera.rotation.clone();
            csave.target   = this.controls.target.clone();
        }
        this.userControls.setScale(scale);
        this.scaleDepth   -= 1;
    }
    
    clickListener(e) {
        
        if (!this.controlClick || e.ctrlKey) {
            // Don't do searches for regular clicks when disabled.
            let res = this.raycast(e);
            this.animation.raycastResult(res);
        }
    }
    
    
    // This method take an event and uses the mouse position to look for
    // nearby objects.  The intersecting or nearby objects are returned.
    
    raycast(e) {
        
        // 1. sets the mouse position with a coordinate system where the center
        //    of the screen is the origin
        const canvas = this.renderer.domElement;
        var rect = canvas.getBoundingClientRect();
        const x = Math.floor( ( e.clientX - rect.left ) / ( rect.right - rect.left ) * canvas.width );
        const y = Math.floor( ( e.clientY - rect.top ) / ( rect.bottom - rect.top ) * canvas.height );
        
        const mouse = {};
        mouse.x =   ( x / canvas.width ) * 2 - 1;
        mouse.y = - ( y/ canvas.height ) * 2 + 1;

        //2. set the picking ray from the camera position and mouse coordinates
        this.raycaster.setFromCamera( mouse, this.camera ); 

        //3. compute intersections
        var intersects = this.raycaster.intersectObjects( this.animation.scene.children, true );

        let results = [];
        let found = {};
    intersectsLoop:
        for ( var i = 0; i < intersects.length; i++ ) {            
            const inter = intersects[i];
            // A composite object may have different names
            // associated with each index.
            let meta;
            let distance;
            if (inter.object.getIndexedMeta) {
                // Points use index, InstancedMesh's using instanceId
                const index = inter.index  || inter.instanceId;
                meta = inter.object.getIndexedMeta(index);
                distance = inter.distanceToRay;
                results.push({meta: meta, distance: distance});
            } else {
                let meta = inter.object.meta;
                if (!meta) {
                    Util.log("Skipping element with no metadata in find");
                    continue;
                }
                let name = meta.name;
                let type = meta.type;
                // There might be some reason this object is hidden and
                // shouldn't be shown, but only check if there's a method for it.
                if (inter.object.animHidden && inter.object.animHidden() ) {
                    continue;
                }
                if (!found[name+type]) {
                    found[name+type] = true;
                    results.push({component: inter.object, meta: inter.object.meta, distance: 0});
                    // Don't look through some types of objects.
                    if (this.animation.blockingTypes) {
                        for (let i=0; i<this.animation.blockingTypes.length; i += 1) {
                            let chkType = this.animation.blockingTypes[i];
                            if (type == chkType) {
                                break intersectsLoop;
                            }
                        }
                    }
                }
            }            
        }
        return results;
    }
    
    // Respond to a resize in the window.
    resize(width, height) {
        if (!this.camera || ! this.renderer) {
            Util.log("Frame.resize called prematurely");
            return;
        }
        this.camera.aspect = width / height;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize( window.innerWidth, window.innerHeight );
    }
};

export default Frame;