import Action from "./Action.js";
class orbit extends Action {
    constructor(ptr, anim, traj) {
        super(ptr,anim,traj);
    }
    
    execute(reverse) {
        let delta   = this.anim.delta;
        let command = this.ptr.elem;
        
        // Going back in time past the origin orbit command.
        // We just stay in orbit and don't change anything.
        if (delta < 0 && command.forward) {
            this.anim.setQuickHTML("Extrapolating trajectory before origin");
            return;
        }
        
        if (delta > 0  || reverse) {
            if (command.forward) {
                // This is an initial stationing request.  We just specify the
                // orbit and continue.
                let here = this;
                // Callback when we are asking for a named orbit.
                let startOrbit = function(bodies) {
                    if (!bodies || bodies.length == 0) {
                        here.anim.setQuickHTML("Unable to resolve body: "+command.forward);
                    } else if (!bodies[0].meta.orbit) {
                        here.anim.setQuickHTML("No orbit known for body.");
                    } else {
                        here.traj.traj = bodies[0].meta.orbit;
                        if (here.traj.state.controllerOff) {
                            here.traj.fixTraj();
                        }
                    }
                };
                
                // Match named orbit.
                this.anim.find(command.forward.trim(), startOrbit);
                                
            } else {
                // Start orbit with the current state.
                this.traj.setToOrbit(this.time);
            }
            
        } else {
            // Going backward.            
            // Find the previous thrust and execute it.
            this.executePrev(this.ptr, ["thrust"]);
        }
    }
}

export default orbit;
