import {Component} from "react";
import TextInput   from "../elems/TextInput.js";
import Util        from "../std/Util.js";
import Constants   from "../std/Constants.js";
import Dump        from "../std/Dump.js";
import RocketController from "../elems/RocketController.js";
import TravellingRocket from "../elems/TravellingRocket.js";
import TrajChooser from "../elems/TrajChooser.js";
import SolarPlanets from "../solarsystem/SolarPlanets.js";
import Sequence from "../trajanim/Sequence.js";
import Command  from "../trajanim/Command.js";
import ThrustAndGrav from "../math/ThrustAndGrav.js";
import Integrator from "../math/Integrator.js";
import QOrb from "../orbits/QOrb.js";
import ProxyComponent from "../components/ProxyComponent.js";

class TrajControl extends Component {
    
    // This class ties together the elements associated with a specific
    // trajectory.  There are four elements that we consider:
    //    The rocket displayed in the animation.
    //    The rocket controller displayed in manual mode.
    //    The rocket sequence of commands
    //    The sequence editor which allows us to modify an existing sequence (TBD).
    
    constructor(props){
        super(props);
        // The animation we are currently running in.
        this.anim          = props.anim;
        this.thrusting     = false;
        this.inSequence    = false;
        this.totalDV       = 0;
        this.movingRocket  = false;
        this.lastState     = null;
        this.lastTime      = this.anim.time;
        this.lastThrust    = null;
        
        // The controller for a rocket we are driving manually.
        this.rocketControl = null;
        
        // The state of the trajectory interface:
        //     init:     haven't chosen run or playback yet.
        //     run:      user controls rocket with direct input.
        //     playback: prewritten script controls system.
        this.state         = {stage: props.initStage, controllerOff: false};
        
        // An element that can provide a state vector given a time.
        this.traj          = null;
        this.initOrbit     = null;
        
        this.proxyName     = null;
        
        // A sequence of commands to control the trajectory.
        // This is written in manual mode and read in playback mode.
        this.sequence      = null; 
        this.count = 100;
    }
    
    startSequence(sequence) {
        this.setState({stage:"run", controllerOff: true}); 
        let strt = sequence.start() - 86400*30;
        // Reset the time for the animation.
        this.anim.setTime(strt);
        this.sequence = sequence;
        
        let render = function(time) {
            this.getCmds(time);
        }
        let proxy = new ProxyComponent(render.bind(this));
        this.proxyName = proxy.name;
        this.anim.addElement(proxy.name, proxy);
    }
        
    setController(controller) {
        this.rocketControl = controller;
        this.rocketControl.setState({dv: this.totalDV});
        if (!this.movingRocket) {
            this.addRocket();
        }
    }
    
    
    // Begin integrating the trajectory.
    setToIntegrate(time, force) {
        
        let dt = this.anim.delta/3;  // Just for grins...
        
        let here = this;
        
        function updateThrust(dt) {
            here.lastThrust = force.lastThrust;
            
            let f = Util.mag(force.lastThrust);
            here.totalDV += dt*f;
            if (here.rocketControl != null) {
                here.rocketControl.setState({dv:here.totalDV});
            } else {
            }
        }
        
        this.traj = new Integrator(force, this.lastState[0], this.lastState[1], time, dt, updateThrust);
        this.thrusting = true;
    }
    
    // Begin a free one-body orbit
    
    setToOrbit(time) {
        // Create an orbit using the current state vectory.
        let str = {t: time, r: this.lastState[0], v: this.lastState[1]};
        let orb = QOrb.makeOrbit(str);
        this.traj = orb;
        this.thrusting = false;
    }
    
    // Add an instantaneous impulse to the existing orbit.
    // (or conceivably at the end of a thrusting period but
    // this will turn off the thrust).
    addImpulse(dv, time) {
        let sign  =  1;
        if (this.anim.delta < 0) {
            sign  = -1;
        }
        let r   = this.lastState[0];
        let v   = Util.vadd(this.lastState[1], dv, sign);
        let str = {t: time, r: r, v: v};
        let orb = QOrb.makeOrbit(str);
        this.lastState = [r, v];
        this.traj = orb;
        this.thrusting = false;
        
        let mag   = Util.mag(dv);
        this.totalDV += sign*mag;
        
        if (this.rocketControl != null) {
            this.rocketControl.setState({dv:this.totalDV});
        }
        this.count = 0;
    }
    
    fixTraj() {
        this.anim.removeElement(this.proxyName);
        this.proxyName = null;
        this.setState({controllerOff: false}, this.addRocket.bind(this));
        this.rocketControl.controllerOff = false;
        this.rocketControl.restart();
    }
    
    addRocket() {
        // After we incorporate the new traj into
        // the current animation we should update
        // the trajectory start for this animation.
        
        if (this.state.controllerOff) {
            return;
        }
        let rock = new TravellingRocket({trajControl:this, name: "MyRocket"});
        this.anim.addElement("MyRocket", rock.getTraveller());
        this.thrusting    = false;
        this.movingRocket = true;
    }
    
    delRocket() {
        this.anim.removeElement("MyRocket");
        this.movingRocket = false;
    }
    
    trajUpdate(t) {
        if (this.initOrbit == null && this.traj != null) {
            this.initOrbit = this.traj;
        }
        
        this.getCmds(t);
    }
    
    getCmds(time) {
        let popper = this.sequence.getPopper(this.lastTime, time);
        
        let cmd;
        let tcmd = this.lastTime;
        let doCmds = false;
        while ( (cmd = popper.next()) != null) {
            doCmds = true;
            
            tcmd = cmd.elem.t;
            if (tcmd != this.lastTime  && this.proxyName == null) {
                this.lastState = this.traj.stateVector(tcmd);
                this.lastTime  = tcmd;
            }
            Command.enact(cmd, this.anim, this);
        }
        if (tcmd != time  && this.traj) {
            this.lastState = this.traj.stateVector(time);
        }
        this.lastTime = time;
        this.count += 1;
        
    }
    
    render() {
        if (this.state.stage == "init") {
            return <TrajChooser trajControl={this} anim={this.anim} />;
            
        } else if (this.state.stage == "run") {
            if (!this.anim.frame.trajControl) {
                this.anim.frame.trajControl = this;
            }
            // Already have a controller
            return <RocketController controllerOff={this.state.controllerOff} anim={this.anim} trajControl={this.anim.frame.trajControl} />
        } else return <span> Programming error: Unknown trajectory controller stage </span>;
    }
    
    startOrbit(orb, planet, time) {
        this.traj = orb;
        this.setState({stage:"run"});
        this.sequence = new Sequence();
        let cmd = new Command(time, "orbit", planet, null);
        this.sequence.add(cmd);        
    }
    
}

export default TrajControl;
