import Gravity from "../math/Gravity.js";
import VThrust from "../math/VThrust.js";
import CompoundForce from "../math/CompoundForce.js";
import Constants from "../std/Constants.js";

class ThrustAndGrav extends CompoundForce {
    constructor(camera, thrust, x, y) {
        super();
        this.add("Grav", new Gravity(Constants.MSun),         false);
        this.add("Thrust", new VThrust(thrust, camera, x, y), true);
    }    
}

export default ThrustAndGrav;