import Kepler from "./Kepler.js";
import Util   from "../std/Util.js";
import Constants from "../std/Constants.js";
import Orbit  from "../orbits/Orbit.js";


/** This is a smaller orbit class that primarily provides a method
 *  for finding the position as a function of time so it uses
 *  a specific set of orbital elements.
 */
class QOrb {

    scale   = 1;
    doLog   = false;
    lastT   = 1.e50;
    lastTA  = 1.e50;
    
    constructor(input) {        
        this.rot = input.rot;    // The rotation info
        this.e   = input.e;      // The eccentricity scalar
        this.a   = input.a;      // The semimajor axis
        this.t0  = input.t0;     // The time of periapsis
        this.per = input.per;    // The period of the orbit
        
        this.precession = false;  // Allow for precession of the orbit.
        this.precessData = null;
        if (this.e < 1) {
            this.Lm   = 2*Math.PI*this.a*this.a*Math.sqrt(1-this.e*this.e)/this.per;
        } else {
            this.Lm   = 2*Math.PI*this.a*this.a*Math.sqrt(this.e*this.e-1)/this.per;
        }
    }
    // Add precession of the line of nodes and the argument of perigee to 
    // the orbit. 
    //   ep0 is the epoch when the arguments in the rot vector are valid.
    //   fOmega and fNu are the frequencies of the prcession (2pi/period)
    //   in the Omega and nu terms.
    addPrecession(ep0, fOmega, fNu) {
        this.precession  = true;
        this.ep0         = ep0;
        this.fOmega      = fOmega;
        this.fNu         = fNu;
    }
        
    // Get the rotation blob, possibly corrected for precession.
    getRot(time) {
        if (!this.precession  || time == null) {
            return this.rot;
        }
        const dt     = time  -  this.ep0;
        const dOmega = this.fOmega*dt;
        const dNu    = this.fNu*dt;
        const updRot = [...this.rot];
        
        // Recall definition of rot:
//                            [co,  -so*ci,  // co = cos(Omega), ci= cos(inclination), .... 
//                             so,   co*ci,  // so = sin(Omega), ...                             
//                             si,           // 
//                             nu]           // argument of perihelion

        // Can fix nu immediately!
        updRot[5] += dNu;
        
        const cd = Math.cos(dOmega);
        const sd = Math.sin(dOmega);
        
        // Now use trig formulas to get new value for sin/cos omega
        // and update the first four values appropriately.
        const so = updRot[2]*cd + sd*updRot[0]; // sin(a+b) = sin(a)cos(b) + sin(b)cos(a)
        const co = updRot[0]*cd - updRot[2]*sd; // cos(a+b) = cos(a)cos(b) - sin(a)*sin(b
        
        // Use update values to update products first.  The inclination does not change.
        updRot[1] *= so/updRot[2];  // i.e., *= so_new/so_old
        updRot[3] *= co/updRot[0];  //       *= co_new/co_old
        
        //  Now we can update the last two terms
        updRot[0] = co;
        updRot[2] = so;
        
        // The inclination does not change during precession, so neither does updRot[4]
        
        return updRot;                
    }
    
    // Get the position as a function of time.
    position(t) {
        // First get the true anomaly as a function of time.
        const ta = this.ta(t);
        // Now get the position given the true anomaly (we pass
        // along t here too in case we need to deal with precession).
        let  r  = this.posTA(ta, t);
        return r;
    }
    
    // Get both position and velocity.
    stateVector(t) {
        
        const ta = this.ta(t);
        let rs = this.posTA(ta, t);
        
        if (this.scale != 1) {
            rs = Util.times(1/this.scale, rs);
        }
        const rot = this.getRot(t);
        return this.stateTA(ta, rs, rot);
    }
    
    stateTA(ta, r, rot) {
        
        const rm = Util.mag(r);
        const sn = Math.sin(ta);
        const p  = this.a*(1-this.e*this.e);
        
        const nu = rot[5];   
        
        const cos = Math.cos(nu+ta);
        const sin = Math.sin(nu+ta);
        
        // Radial velocity
        const vrad = Util.times(this.Lm*this.e*sn/(rm*p) , r);
                        
        const f    = this.Lm/rm;
        
        // Tangential velocity
        let vtan = [   -f*(rot[0]*sin - rot[1]*cos),
                       -f*(rot[2]*sin - rot[3]*cos),
                        f*rot[4]*cos ];
                      
        // Total velocity
        let vtot = Util.vadd(vrad, vtan);
        
        // Return two 3 vectors
        return [r,vtot];
    }
    
    // Get the true anomaly from the time.
    // This function requires the phase of the orbit to be known.
    ta(t) {       
        let e = this.e;
        let ma;
        
        ma = (t - this.t0) / this.per * 2 * Math.PI;
        if (e < 1) {
            ma = ma % (2 * Math.PI);
        }
        
        // Convert mean anomaly to  eccentric (or hyperbolic) anomaly
        const ea  = Kepler(ma, e);
        
        // Now go from eccentric/hyperbolic anomaly to true anomaly
        let tan;
        if (e < 1) {
            tan = Math.tan(ea/2);
        } else {
            tan = Math.tanh(ea/2);
        }
        
        const tmp = Math.sqrt((1 + e) / Math.abs(1 - e)) * tan;

        let ta =  2 * Math.atan(tmp);
        return ta;        
    }
    
    // Get the position as a function of the true anomaly.
    // This function may be used even if the phase of the orbit
    // is not known, but no precession can be applied then.
    posTA(ta, t) {
        const e = this.e;
        
        // Get the magnitude of the radius vector
        // 
        // Note Math.abs to handle hyperbolic orbits.
        // since we want the radius to be positive in all cases.
        // Shouldn't really be necessary but we may sometimes have
        // used a positive semimajor axis for a hyperbolic trajectory.
        const rm = Math.abs(this.a*(1 - e * e)) / (1 + e * Math.cos(ta))* this.scale;
        

        // TA and nu are rotations along the same axis so we can
        // combine them and we're left with a somewhat simpler
        // transformation than the full ZXZ rotation matrix with nu,i,Omega.
        // We also put rm in here since it saves a multiplication.

        // First extract nu from the rot blob.
        const rot = this.getRot(t);
        const nu  = rot[5];
        
        const cos = rm*Math.cos(nu+ta);
        const sin = rm*Math.sin(nu+ta);
        // Now use rot information which was specifically designed
        // to make this step as efficient as possible/
        let vec = [
                     rot[0]*cos + rot[1]*sin,
                     rot[2]*cos + rot[3]*sin,
                     rot[4]*sin
                    ];
        return vec;
    }
    
    // Get the orbit curve as a sequence of positions.
    // For closed orbits start at the periapsis, while
    // for open orbits we are centered at periapsis.
    // Note that this function does not require the phase
    // of the orbit to be known.
    orbitXYZ(n, t) {
        
        // For hyperbolic orbits not all values of TA are allowed...
        var taMin = 0;
        var taMax = 2*Math.PI;
        if (this.e > 1) {
            // trying to get ta[i] = -ta[n-i];
            // bit we need to come in a little since the radius at the limits is infinity.
            taMax = Math.acos(-1/this.e);
            const tdelta = (2*taMax)/n;
            taMax -= tdelta/2;
            taMin  = -taMax;
        }
        
        var ta = taMin;
        const data  = [];
        const delta = (taMax-taMin)/n;
        for (var i=0; i<n; i += 1) {
            var p = this.posTA(ta, t) // Note that we may not have a time here
                                      // Precessing orbits will then be as of epoch of ephemeris.
            data.push(p);
            ta += delta;            
        }
        return data;        
    }
    
    static makeOrbit(params) {
        let initOrbit = new Orbit();
        initOrbit.addStructure(params);
        initOrbit.computeParameters(true);
        let orb = initOrbit.quickOrbit();
        return orb;
    }
    
    // Special code to derive orbit from state.
    // This uses same methods as in the Origins.js file, but
    // strips out all of the generic parameter overhead and
    // follows a known sequence for deriving the necessary
    // orbit parameters.
    static stateOrbit(params) {
        
        // User can specify central mass using either GM or M, 
        // but G alone cannot be altered (i.e., would be ignored).
        if (params.GM) {
            params.M = params.GM/Constants.G;
        }        
        let M     = Constants.MSun;
        if (params.M) {
            M = params.M;
        }
        
        // Require full state to be given.
        if (!params.r || !params.v || params.t == null) {
            throw new Error("State not passed to stateOrbit");
        }
        // Position
        let r     = params.r;
        let rmag  = Util.mag(r);
        // Velocity
        let v     = params.v;
        let vmag  = Util.mag(v);
        // Angular momentum
        let L     = Util.cross(r,v);
        let Lmag  = Util.mag(L);
        
        // Inclination
        let dot   = Util.dot(L, [0, 0, 1]) / Lmag;
        let i     = Math.acos(dot);
        
        // Line of nodes
        let pnode = Util.cross([0,0,1], L);
        let pmag  = Util.mag(pnode); 
        if (  (pmag / Lmag) < Constants.PLANAR_RATIO) {
             pnode = [1,0,0];
        } else {
             pnode = Util.times(1/pmag, pnode);             
        }
        pmag = 1;
        
        // Energy
        let E      = vmag * vmag / 2 - Constants.G*M/rmag;
        
        // Semimajor axis
        let a      = -0.5 * Constants.G * M / E;
        
        // Longitude of ascending node.
        let px = pnode[0];
        let py = pnode[1];
        let omega = 0;
        if (Math.abs(px) > Constants.PLANAR_RATIO || Math.abs(py) >= Constants.PLANAR_RATIO) {
            omega = Math.atan2(py, px);
        }
        
        // Eccentricity vector
        let f1    = Util.times(1/(Constants.G*M), Util.cross(v, Util.cross(r,v)));
        let f2    = Util.times(-1/rmag, r);
        let evec  = Util.vadd(f1,f2);
        let ecc   = Util.mag(evec);
        
        // Semiminor axis
        let sign = 1;
        if (ecc > 1) {
                    sign = -1;
        }        
        let b = a*Math.sqrt(sign*(1 - ecc * ecc));
        
        // Period
        if (a * b === 0 || Lmag === 0) {
            throw new Error("Radial orbit in stateOrbit");
        }
        let per =  2*Math.PI * a * b / Lmag;
        if (per < 0) {
            per = -per;
        }
        
        // True anomaly
        let nu = 0;
        if ((pmag > Constants.SHAPE_RATIO) && 
            (ecc  > Constants.SHAPE_RATIO)) {
            let cs = Util.dot(pnode, evec) / pmag / ecc;
            nu = Math.acos(cs);
            // Check whether we are in 0-PI or PI-2PI
            const cross = Util.cross(pnode, evec);
            const dir   = Util.dot(cross, L);
            if (dir < 0) {                
                nu = 2*Math.PI - nu;
            }
        }
        
        // Rotation parameters
        let rot = Util.anglesToRot({Omega: omega,
                                     nu:   nu,
                                     i:    i})
                            ;
                            
        // True anomaly
        let emag = Util.mag(evec);
        let ta = 0;
        if (emag > Constants.SHAPE_RATIO) {
            let dot   = Util.dot(r, evec)/(rmag*ecc);
            const cross = Util.cross(evec,r);  // Order matters here.
            if (dot > 1) {
                 dot = 1;
            } else if (dot < -1) {
                 dot = -1;
            }
            ta = Math.acos(dot);
            if (Util.dot(cross,L) < 0) {
                ta = 2*Math.PI - ta;
            }
        }
        
        // Eccentric anomaly
        sign = 1;
        if (ecc > 1) {
            sign = -1;
        }
        let ea = Math.sqrt(sign*(1 - ecc) / (1 + ecc)) * Math.tan(ta / 2);
        if (ecc < 1) {
            ea = 2*Math.atan(ea);
            // For periodic orbits make range 0 to 2PI
            if (ea < 0) {
                ea = 2 * Math.PI + ea;
            }
        } else {
            // For unbound we want will leave range centered on 0.
            ea = 2*Math.atanh(ea);
        }
        
        // Mean anomaly
        let ma = ea;
        if (ecc > Constants.SHAPE_RATIO) {
            // Kepler's equation in the easy direction
            if (ecc < 1) {                    
                ma = ea - ecc * Math.sin(ea);
            } else {
                ma = ecc*Math.sinh(ea) - ea;
            }
        }
        
        // Finally set the phase of the orbit.
        let dt = ma*per/(2*Math.PI);
        let t0 = params.t-dt;
        
        
        //  Now we have everything we need for a full orbit.
        let orbVal = {
            a:   a,     // semimajor axis
            e:   ecc,   // eccentricity 
            rot: rot, // orientation information (omega,i,nu)
            t0:  t0,   // time of periapsis
            per: per  // period
        };
        
        return new QOrb(orbVal);
    }
}

export default QOrb;