import Action from "./Action.js";

class speed extends Action {
    constructor(ptr, anim, traj) {
        super (ptr, anim, traj);
    }
    // Change the speed of the animation.  The argument
    // is the number of steps to increase (decrease if negative)
    // the speed when going forward.  Each step increases the time
    // delta by sqrt(2).  Negative values slow down.  When going backwards
    // the negative if the value is used.  
    execute() {
        let numb = parseInt(this.ptr.elem.forward);
        let faster = true;
        if (this.anim.delta < 0) {
            numb = -numb;
        }
        if (numb < 0) {
            numb   = -numb;
            faster = false;
        }
        for (let i=0; i<numb; i += 1) {
            this.anim.speed(faster);
        }
    }
}

export default speed;

