import  {Component, createRef}  from "react";
import Util             from "../std/Util.js";
import Dump             from "../std/Dump.js";
import Constants        from "../std/Constants.js";
import ParameterFactory from "../orbits/ParameterFactory.js";
import Parameter        from "../orbits/Parameter.js";
import Orbit            from "../orbits/Orbit.js";

import UnitLegend       from "./UnitLegend.js";

import Row       from "./Row.js";
import TextInput from "../elems/TextInput.js";

class OrbitCalculator extends Component {
    // The value is the underlying value of the parameter in unscaled units.
    // The entry is how the user entered it.
    // Validated indicates that the parameter has not been modified
    // since the last time it was validated.  Computed indicates
    // that the parameter was calculated, not entered.
    // unitIndex is the current index for the unit being used for this
    // parameter.  InitUnit is the unit index in which a non-computed parameter
    // was entered.
    emptyRow = {value: null, entry: null, validated: false, computed: false,
                      unitIndex: -1, initUnit: -1};
    constructor(props) {
        super(props);
        this.params    = ParameterFactory.legalParameters;
        this.orbit     = null;
        this.animFactory = null;
        this.displayCount = 0;
        // Note that the states for each row are indexed
        // by the unique row names.
        this.state = {};
        for (let name in this.params) {
            let param = this.params[name];
            let empty = {...this.emptyRow};
            this.state[param.name] = empty;
        }
        // Extra state here where we need to make sure we don't use
        // use the same key as an orbital parameter.
        this.state.highPrec = false;
        this.state.orbitName = null;
        this.state.status   = "";
    }
    
    // Whenever one of the rows is updated...
    updateRow(name, state) {
    
        let here = this;
        let oldState = this.state[name];
        
        // Delete from the orbit as well.
        if (!state.validated && this.orbit && this.orbit.parameters[name]) {
            let toDelete = [name];
            let deletes = {};
            deletes[name] = true;
            for (let i=0; i<toDelete.length; i += 1) {
                let check = toDelete[i];
                for (let xname in this.orbit.parameters) {
                    if (deletes[xname]) {
                        continue;  // Already deleting this one.
                    }
                    let orbPar = this.orbit.parameters[xname];
                    if (orbPar.generator) {
                        let prereqs = orbPar.generator.reqs;
                        for (let i=0; i<prereqs.length; i += 1) {
                            let prereq = prereqs[i];
                            if (prereq === check) {
                                deletes[xname] = true;
                                // extends toDelete so outer for loop will go longer.
                                toDelete.push(xname);
                            }
                        }                    
                    }
                }
            }
            this.orbit.delParameter(name);
            toDelete.shift();  // Get rid of first entry which we have already handled.
            for (let i=0; i<toDelete.length; i += 1) {
                let name = toDelete[i];
                this.orbit.delParameter(name);
                let upd   = {};
                upd[name] = this.emptyRow;
                this.setState(upd);
            }
        }
        let newState = {};
        newState[name] = state;
        this.setState(newState);
    }

    // It seems like the first time we create the objects we 
    // can get proper references to them, but later entries don't
    // seem to work.  So we only compute the references the first
    // time.  Probably something dodgy going on here.
    render() {
        const here  = this;
        let fixed  = [];
        let varies = [];

        for (var name in this.params) {
            const param = this.params[name];
            var elem = (<Row param={param} key={name}
                             calc={this} 
                             highPrec={this.state.highPrec} />);
                            
            if (param.varies) {
                varies.push(elem);
            } else {
                fixed.push(elem);
            }
        }
        
        return (
<div>
    <h2><a href="./calc.html" target="help"> Orbit Calculator </a></h2>
    Name for orbit:&nbsp; 
    <TextInput placeholder="Optional name"
       callback={function(name) {here.setOrbitName(name);}} />
    <p />
    Status: <div>{this.state.status}</div>
    <p />
    
  <table><tbody>
    <tr key="hdr"><th>Name</th><th>Value</th><th>Units</th><th align="left">Description</th></tr>
    <tr key="div1"><td colSpan='4' align='center'> <i>Orbit Constants</i></td></tr>
    {fixed}
    <tr key="div2"><td colSpan='4' align='center'><i>Orbit Variables</i></td></tr>
    {varies}
    </tbody>
  </table>
  <table><tbody>
            <tr>
            <td key="1"><button
              onClick={here.compute.bind(here)}
            >Compute</button></td>
            <td key="2"><button
              onClick={here.displayOrbit.bind(here)}
            >Render orbit</button></td>
            <td key="3"><button
               onClick={here.clearAll.bind(here)}
            >Clear all</button></td>
            <td key="4"><button
               onClick={here.clearVarying.bind(here)}
            >Clear variables only</button></td>
            <td key="5">High precision?<input type="checkbox" 
                onChange={here.precChange.bind(here)} checked={here.state.highPrec}/></td>
            </tr>
      </tbody>
    </table>
    <p />
    <hr/>
    <UnitLegend />
    </div>    
    );
    }
    
    precChange(e) {
        this.setState({highPrec: e.target.checked});
    }
    // This may be from an invoking page.
    setAnimFactory(animFactory) {
        this.animFactory = animFactory;
    }
    
    clearAll() {
        this.orbit = null;
        this.clear();
    }
    
    clearVarying() {
        this.clear(true);
    }
    
    clear(varying) {
        let filter = (varying != null);
        let copy = {...this.state};
        let changed = false;
        for (let name in this.params) {
            let param = this.params[name];
            if (!filter || (param.varies === varying)) {
                let oldState = copy[name];
                // Skip update if empty.
                if (oldState.entry || oldState.validated) {
                    if (this.orbit && this.orbit.parameters[name]) {
                        this.orbit.delParameter(name);
                    }
                    changed    = true;
                    copy[name] = {...this.emptyRow};
                }
            }            
        }
        if (changed) {
            this.setState(copy);
        }
        this.status("");
    }
    
    compute() {
        // Compute all of the parameters that we can given the 
        // current inputs.
        
        // First make sure all current inputs are valid.
        if (!this.orbit) {
            this.orbit = new Orbit(this.orbitName);
        }
        let newState = {...this.state};
        if (!this.checkOrbitParams(newState) ) {
            return;
        }
        
        let updated = true;
        let totalUpdates = 0;
        
        while (updated) {
            updated = false;
            // Loop over all parameters and see if we can update them.
            for (let name in this.params) {
                let param = this.params[name];
                if (newState[name].value == null && this.tryRowUpdate(param, newState, true)) {
                    updated       = true;
                    totalUpdates += 1;
                }
            }
        }
        // Now do the accumulated updates.
        this.setState(newState);
        this.status(totalUpdates+" parameters computed");
    }
    
    status(text) {
        this.setState({status:text});
    }
    
    checkOrbitParams(newState) {
        let orbAdded = [];
        for (let name in this.params) {
            let param = this.params[name];
            let rowState = {...newState[name]};
            if (rowState.entry != null) {
                if (!rowState.validated) {
                    let rowCh = this.rowLegal(param, rowState);
                    if (rowCh.status == "error") {
                        // Remove anything we might have added to the orbit before
                        // we found this error.
                        if (this.orbit) {
                            for (let i=0; i<orbAdded.length; i += 1) {
                                this.orbit.delParameter(orbAdded[i]);
                            }
                        }
                        return false;
                    }
                    if (rowCh.status == "validated") {
                        newState[name] = rowCh.rowState;
                        orbAdded.push[name];
                    }
                }
            }
        }
        this.status("");
        return true;
    }
    
    tryRow(param) {
        let name = param.name;
        let newState = {...this.state};
        if (this.orbit == null) {
            this.orbit = new Orbit(this.orbitName);
        }
        if (this.checkOrbitParams(newState)) {
            this.tryRowUpdate(param, newState, false);
            this.setState(newState);
        }
    }
        
    tryRowUpdate(param, newState, cycle) {
        
        let name = param.name;
        if (this.orbit == null) {
            orbit = new Orbit(this.orbitName);
        }

        let rowState = newState[name];
        if (rowState.value == null) {
            let newPar = this.orbit.getParameter(name, cycle);
            if (newPar != null) {
                let unitIndex = -1;                
                let val = newPar.value;
                if (param.units) {
                    unitIndex = this.getDftUnit(param);
                }
                let initUnit = unitIndex;
                if (unitIndex >= 0) {
                    let unit = param.units[unitIndex];
                    if (unit.scale != 1) {
                        val /= unit.scale;
                    }
                }
                
                // Note that computed values are shown truncated for all units.
                // so entry is never used.
                let newRowState = {value: val, entry: null, validated:true, computed: true, 
                    initUnit: unitIndex, unitIndex: unitIndex};
                newState[name] = newRowState;
                return true;
            }
        }
        return false;
    }
    
    displayOrbit() {
        this.compute();
        this.displayCount += 1;
        var qorb = this.orbit.quickOrbit();
        if (qorb == null) {
            this.status("Insufficient known orbital parameters to determine orbit");
        } else {
            var name = this.orbit.getOrbitName();
            if (!name) {
                name = "UserOrbit";
            }
            
            name += this.orbitCount;
            
            // Use the central mass to decide where to show the orbit.
            var mass = this.orbit.getParameter("M").value;
            var mrat = mass/Constants.MSun;
            let anim = null;
            if (mrat > 0.999999 && mrat < 1.000001) {
                anim = "Solar System";
            } else {
                mrat = mass/Constants.MEarth;
                if (mrat > 0.999999 && mrat < 1.000001) {
                    anim = "The Earth and environs";
                }                
            }
            
            if (anim) {
                this.animFactory.getAnimation(anim).addExternalOrbit(name, qorb);
            } else {
                this.status("Central orbit mass not consistent with Earth or sun")
            }
        }        
    }
    
    setOrbitName(name) {
        this.orbitName = name;
    }

    getDftUnit(param) {
        if (!param.units) {
            return -1;
        }
        for (let i=0; i<param.units.length; i += 1) {
            const unit = param.units[i];
            if (unit.unit == "d" || (unit.scale == 1  && unit.unit != 'rad')) {
                return i;
            }
        }
        return -1;
    }
    
    // Check that a row is in a legal state    
    rowLegal(param, state) {
        let val     = state.entry;
        if (state.validated) {
            return {status: "prevalid"};
        }
        if (val == null || val.length == 0) {  // Empty... which is fine.
            return {status: "empty"};  // There's nothing wrong here.  It's just empty.
        }
        let result = this.rowCheck(param, state);
        if (result == null) {
            return {status: "error"};
        } else {
            return {status: "validated", rowState: result};
        }
    }
    
    rowCheck(param, state) {
        let val   = state.entry;
        let entry = state.entry;
        val = val.trim();        
        // Allow commas as separators.
        // Get rid of [] that may have been displayed.
        val = val.replace(/,/g, ' ');
        val = val.replace('[', '');
        val = val.replace(']', '');

        // Replace multiple spaces (or tab or ...) with a single space.
        val       = val.replace(/\s+/g, " ");
        
        var elems = val.split(" ");
        var dim   = null;
        
        // May have a unit string.
        if (elems.length === 2 || elems.length === 4 || elems.length === 10) {
            dim = elems.pop().toLowerCase();
        }
        var unit = null;
        
        let unitIndex = -1;
        if (dim && param.units) {            
            var i;
            for (i = 0; i < param.units.length; i += 1) {
                const xunit = param.units[i];
                if (xunit.unit.toLowerCase() === dim) {
                    unitIndex = i;
                    unit = xunit;
                    break;
                }
            }
            
            if (unitIndex < 0) {
                this.verr(param.name, "Unknown unit:"+dim);
                return null;
            }
        }
        if (unitIndex < 0 && param.units) {
            unitIndex = this.getDftUnit(param);
            unit = param.units[unitIndex];
        }
        let initUnit = unitIndex;
        
        var scale  = 1;
        var offset = 0;
        if (unit) {
            scale = unit.scale;
            if (unit.offset) {
                offset = unit.offset;
            }
        }
        let parameter;
        if (elems.length === 1) {
            if (param.dim !== Parameter.SCALAR) {
                this.verr(param.name, "Scalar specification for non-scalar parameter");
                return null;
            }
            val = elems[0];
            if (isNaN(val)) {
                this.verr(param.name, "Not a numerical value");
                return null;
            }
            val = (Number.parseFloat(val)-offset)*scale;
            parameter = ParameterFactory.genParam(param.name, 
                               val, unit);

        } else if (elems.length === 3) {
            if (param.dim !== Parameter.VECTOR) {
                this.verr(param.name, "Vector specification for non-vector");
                return null;
            }
            if (isNaN(elems[0])  || isNaN(elems[1] || isNaN(elems[2]))) {
                this.verr(param.name, "Not a numeric vector");
                return null;
            }
            parameter = ParameterFactory.genParam(param.name,
                    [Number.parseFloat(elems[0]) * scale,
                     Number.parseFloat(elems[1]) * scale,
                     Number.parseFloat(elems[2]) * scale], unit);
            
        } else {
            this.verr(param.name, "Unexpected number of tokens in parameter:" + elems.length);
            return null;
        }
        
        let rowState = {value: parameter.value, entry: entry, validated:true,
            computed: false, initUnit: initUnit, unitIndex: unitIndex};
        
        if (!this.orbit) {
            this.orbit = new Orbit(this.orbitName);
        }
        this.orbit.addParameter(parameter);

        return rowState;
    }
    
    verr(name, msg) {
        this.status("Error: "+name+" -- "+msg);
    }
    
    cycleUnits(name, units) {
        let newState = {...this.state[name]};
        newState.unitIndex += 1;
        if (newState.unitIndex >= units.length) {
            newState.unitIndex = 0;
        }
        this.updateRow(name, newState);
    }

}

export default OrbitCalculator;