// Sorted sequence of elements implemented
// in a doubly linked list.
// This sequence maintains a state that includes
// a current time and position within the sequence.
// Users can ask for a 'popper' iterator which returns
// the elements within a range.  Note that this may change as
// elements are processed.
import Util from "../std/Util.js";

// Could make it a member of the sequence class, but decided not to.
// This returns
//    NullPopper if there are no elements in the requested range
//    EqualPopper if a single time is requested and there is at least one element at that moment
//    RangePopper when there is one or more elements in the requested finite range.
function getPopper(seq, from, to) {
    let oldTime = seq.currentTime;
    seq.currentTime = to;
    let mn,mx;
    if (from < to) {
        mn = from;
        mx = to;
    } else {
        mx = from;
        mn = to;
    }
        
    // Usual case.  No events.        
    if (seq.cursor.elem.t <= mn   && seq.cursor.next.elem.t > to) {
        return new NullPopper();
    }
    let forward = to>from;
    
    // Confirm that the cursor is in the right place.
    if (forward) {
        while (from <= seq.cursor.elem.t) {
            seq.cursor = seq.cursor.prev;
        }
        while (from >  seq.cursor.next.elem.t) {
            seq.cursor = seq.cursor.next;
        }
    } else {
        while (from <= seq.cursor.elem.t) {
            seq.cursor = seq.cursor.prev;
        }
        while (from > seq.cursor.next.elem.t) {
            seq.cursor = seq.cursor.next;
        }
    }
    
    while (from < seq.cursor.elem.t  || (!forward && from == seq.cursor.elem.t) ) {
        seq.cursor = seq.cursor.prev;
    }
    
    while (from > seq.cursor.next.elem.t || (forward && from == seq.cursor.elem.t) ) {
        seq.cursor = seq.cursor.next;
    }
    
    // Might have jumped around, so just check agin.   
    if (seq.cursor.elem.t <= mn  && seq.cursor.next.elem.t > mx) {
        return new NullPopper();
    }
    
    // Special case we want the events at a particular time.        
    if (from == to) {
        let t = from;
        // Must be one of the boundaries due to our check earlier.
        if (t == this.seq.cursor.elem.t) {
            // Do nothing
        } else if (t == this.seq.cursor.next.elem) {
            // Adjust cursor
            this.seq.cursor = this.seq.cursor.next;
        } else {
            // Nothing found at current time.
            return new NullPopper();
        }
        // Do we want the events from first to last or last to first?
        let forward =  t >= oldTime;
        if (forward) {
            while(seq.cursor.prev.elem.t == t) {
                seq.cursor = seq.cursor.prev;
            }
        } else {
            while (seq.cursor.next.elem.t == t) {
                seq.cursor = seq.cursor.next;
            }
        }        
        return EqualPopper(seq.cursor, t, forward);
    }
    
    // We've got at least one event in range...
    return new RangePopper(seq, from, to);
}
        
class RangePopper {
    constructor(seq, from, to) {
        this.seq      = seq;
        this.from     = from;
        this.to       = to;
        this.forward  = this.to >  this.from;
        this.first    = !this.forward;  // Special check for first
                                        // iteration in reverse.
        // Find the interval the start, from, is in.  If we are moving forward,
        // then from can be at the lower boundary.  If backward, then from may
        // be at the upper boundary.
        let curs = seq.cursor;
        if (this.forward) {
            while (from < curs.elem.t) {
                curs = curs.prev;
            }
            while (from >= curs.next.elem.t) {
                curs = curs.next;
            }
        } else {
            while (from <= curs.elem.t) {
                curs = curs.prev;
            }
            while (from > curs.next.elem.t) {
                curs = curs.next;
            }
        }
        this.curs = curs;
    }
    
    next() {
        
        if (this.first) {
            // Check events that occur at the start time of a reverse interval.
            // We only do this in reverse, because the start time is not included
            // in forward interval (the end time is not included in a reverse interval).
            if (this.curs.next.elem.t == this.from) {
                let res = this.curs.next;
                if (this.curs.elem.t != this.from) {
                    // This is the last (in reverse order) event at the time 'from'
                    this.first = false;
                } else {
                    // We have more events at the 'from' time.
                    this.curs = this.curs.prev;
                    this.seq.cursor = this.curs;
                }
                return res;
            }
        }
        // If we get here, then we can just compare to with the appropriate
        // end of the current interval.
        if (this.forward) {
            this.curs = this.curs.next;            
            if (this.curs.elem.t > this.to) {
                // Next element is after the end of the interval
                // Note that if it is at the end, we process it.
                return null;
            } else {
                // The event at the end of the current interval is included...
                this.seq.cursor = this.curs;                
                return this.curs;
            }
        } else {  // Reverse
            this.curs = this.curs.prev;   
            if (this.curs == null) {
                return null;
            }
            if (this.curs.next.elem.t <= this.to) {
                // The end of the sequence is before the current interval
                // Note that if it is we do NOT process it when it is equal.
                console.log("    Next returns null");
                return null;
            } else {
                // The event at the beginning of the current interval is included.
                this.seq.cursor = this.curs;
                return this.curs.next;
            }
        }
    }
}

class NullPopper {
    next() {
        return null;
    }
}

class EqualPopper {
    constructor(cursor, t, forward) {
        this.curr    = cursor;
        this.t       = t;
        this.forward = forward;
        this.first   = true;
        // Do we want to go to the front or back...
        if (forward) {
            while (this.curs.prev.elem.t == t) {
                this.curs = this.curs.prev;
            }
        } else {
            while (this.curs.next.elem.t == t) {
                this.curs = this.curs.next;
            }
        }
    }
    next() {
        // We do the move first, so that we can add events dynamically, so
        // we've completely finished processing the current event before we move
        // on.  We skip the move for the first iteration.
        if (!this.first) {
            if (this.forward) {
                this.curr = this.curr.next;
            } else {
                this.curr = this.curr.prev;
            }
        }
        this.first = false;
        if (this.curr.elem.t != this.t) {
            return null;
        } else {
            return this.curr;
        }       
    }
}

class Sequence {
    
    
    constructor() {
        
        // These following two elements bound the sequence but are never returned.
        this.first       = {elem: {command:"START",t: Number.NEGATIVE_INFINITY}, prev: null,       next: null};
        this.last        = {elem: {command:"END",  t: Number.POSITIVE_INFINITY}, prev: this.first, next: null};        
        // Link the endpoints together to initialize the list
        this.first.next  = this.last;
        
        // The following fields are the state of the sequence (as distinguished
        // from its content).  These change as we move up and down through the sequence
        // processing elements.
        
        // The time currently associated with the sequence.
        this.currentTime = -1.e100;
        // A pointer to the beginning of the interval that contains the current time.
        // Note that the interval is open at the lower limit, so the current time
        // should never equal the time of the the element the cursor is pointing at.
        this.cursor     =  this.first;
        // Number of 'real' elements in the sequence.
        this.size       = 0;
    }
        
    // Add an element into the sequence.
    add(element) {
        if (element.t == null) {
            Util.log("No time in element"+element.toString());
            return null;  // Ignore nulls.
        }
        
        let curr = this.last;
        
        // Insert.  We make it efficient
        // if the sequence it built in time order, but 
        // don't do anything special ortherwise.
        // We don't expect very long lists.
        while (curr.elem.t > element.t) {
            curr = curr.prev;
        }
        let oldNext  = curr.next;
        curr.next    = {elem: element, next: oldNext, prev: curr};
        oldNext.prev = curr.next;
                
        this.size += 1;
        return curr;  // Return pointer to the element we added.
    }
    
    
    // Get a iterator over the elements in the range.
    // A popper object with a next() method is returned which
    // returns null when there are no more objects.
    getPopper(from, to) {
        return getPopper(this, from, to);
    }
    
    
    // Delete an element of the sequence.
    del(element) {
        
        // Is this an element in the cursor?
        // These require a little extra care.
        if (element === this.cursor.elem) {
            let old = this.cursor;
            this.cursor  = old.prev;
            old.prev.next = old.next;
            old.next.prev = old.prev;
            this.size -= 1;
            return true;
        } else if (element === this.cursor.next) {
            let old = this.cursor.next;
            old.next.prev = old.prev;
            old.prev.next = old.next;
            this.size -= 1;
            return true;
        }
        
        // Otherwise just find the element in the sequence and excise it.
        let curr = this.last.prev;
        let treq = element.elem.t;
        while (curr.elem.t >= treq) {
            if (curr.elem === element.elem) {
                curr.prev.next = curr.next;
                curr.next.prev = curr.prev;
                // Successful deletion
                this.size -= 1;
                return true;
            } else {
                curr = curr.prev;
            }
        }
        
        // Not found.
        return false;
    }
    
    // Get the time of the first (real) element.
    // Return null if there are no real elements.
    start() {
        if (this.first.next === this.last) {
            return null;
        } else {
            return this.first.next.elem.t;
        }
    }
    
    // Get the time of the last (real) element.
    // Return null if there are no real elements.
    finish() {
        if (this.last.prev === this.first) {
            return null;
        } else {
            return this.last.prev.elem.t;
        }
    }
    
    toString() {
    
        let res = "";
        let curr = this.first;
        while (curr.next != this.last) {
            curr = curr.next;
            let row = curr.elem.toString();
            res += row+"\n";           
        }        
        return res;
    }
}

export default Sequence;