import Util from      "../std/Util.js";

class Gradient {
    
    constructor(mag, grad) {
        this.mag  = mag;
        this.grad = grad;        
    }
    
    a(x,v,t) {
        let dir = Util.unit(this.grad.gradient(x, v, t));
        let acc = Util.times(this.mag, dir);
        return acc;
    }
}

export default Gradient;

