import {CircleGeometry, Group, DoubleSide, MeshBasicMaterial, Mesh} from "three";
import {Vector3, Line, LineBasicMaterial, SphereGeometry, LineDashedMaterial, BufferGeometry} from "three";
class CoordSys extends Group {
    constructor() {        
        super();
        const geometry = new CircleGeometry(2,16);
        const material = new MeshBasicMaterial({
            color: 0xaa0000,
            wireframe: true,
            side: DoubleSide
        });
        
        const circle = new Mesh(geometry, material)        
        this.add(circle);
        
        let axis = function(elem, x, color) {
        
        // Solid line positive, dashed line negative.
            let points = []
            const solid = new LineBasicMaterial({color: color});
            const dash  = new LineDashedMaterial({color: color, dashSize:0.25, gapSize:0.25});
            points.push( new Vector3( x[0],  x[1],  x[2]) );
            points.push( new Vector3(0,0,0));
            let geom = new BufferGeometry().setFromPoints( points );
            elem.add(new Line(geom, solid));
            points = [];
            points.push( new Vector3( -x[0],  -x[1],  -x[2]) );
            points.push( new Vector3(0,0,0));
            geom = new BufferGeometry().setFromPoints( points );
            let dline = new Line(geom, dash);
            dline.computeLineDistances();
            elem.add(dline);
        };
        
        axis(this, [3,0,0], 0xffff00);
        axis(this, [0,3,0], 0x00ff00);
        axis(this, [0,0,3], 0x0000ff);
           
        let addS = function(elem, x) {
            let sg = new SphereGeometry(0.1, 8, 8);
            let sm = new MeshBasicMaterial({wireframe: false, color: 0xff0000 });            
            const sphere   = new Mesh(sg, sm);
            sphere.position.copy(new Vector3(x[0], x[1], x[2]));
            elem.add(sphere);
            return sphere;        
        };
        
        this.spherex = addS(this, [2,0,0]);
        this.spherey = addS(this, [0,2,0]);
        this.spherez = addS(this, [0,0,2]);
    }
}
export default CoordSys;

