import  {Component}  from "react";

import Animation     from    "../elems/Animation.js";
 
import Util          from    "../std/Util.js";
import Dump          from    "../std/Dump.js";
import Constants     from    "../std/Constants.js";
import LocalGalaxy   from    "./LocalGalaxy.js";

import SolarPlanets   from    "../solarsystem/SolarPlanets.js";
import ssData         from   "../data/PlanetaryData.js";

import ParameterFactory from "../orbits/ParameterFactory";
import Orbit         from    "../orbits/Orbit.js";
import loadMPCData  from    "../parsers/ParseMPC.js";

import OrbitPlot     from    "../solarsystem/OrbitPlot.js";
import Planet        from    "../solarsystem/Planet.js";
import Ring          from    "../solarsystem/Ring.js";
import SmallBodies   from    "../solarsystem/SmallBodies.js";
import Sun           from    "../solarsystem/Sun.js";
import Missions      from    "../solarsystem/Missions.js";
import Rocket        from    "../components/Rocket.js";

import SysInfo       from    "../std/SysInfo.js";

import {TextureLoader, sRGBEncoding, EquirectangularReflectionMapping} from "three"; 
import {CubeTextureLoader} from "three"; 

import Gravity from "../math/Gravity.js";
import Oriented from "../math/Oriented.js";

class SolarSystem extends Animation {
    
    
    constructor(inputs) {
        super(inputs);
        
        const fields = {
            centerBody:    "Sun",
            name:          "Solar System",
            sceneCoord:    "Ec",
            baseAnimation: "Local Galactic Neighborhood",
            longClickTime: 1500,  // How long a mouse click needs to be to be classified as a long click.
            maxScale: 13.5,       // Maximum scale for simulation (about 30 billion km)
            minScale: 10.5        // Minimum scale for simulation (about 5 million km)
        };
        
        for (var key in fields) {
            if (!inputs || !inputs[key]) {   // Don't override anything specified by user.
                this[key] = fields[key];
            }
        }
        
        if (!inputs.planets) {
            this.planets = Object.keys(ssData.Sol.planets);
        }
        
        this.addSun();
        this.addPlanets();
        this.sceneScale = Constants.AU;
        this.addSmallBodies();
        this.time = new Date().getTime()/1000 - Constants.Epoch0;
        
        this.addTimeDisplay();
        
        let miss = Missions(ssData.Sol.missions, this.time);
        let oldQuiet = this.quiet;
        if (miss.length > 10) {
            this.quiet = true;
        }
        for (let i=0; i<miss.length; i += 1) {
            this.addElement(miss[i].name, miss[i]);
        }
        this.quiet = oldQuiet;
        
        if (miss.length > 0) {
            Util.log("Added "+miss.length+" spacecraft missions");
        }
        this.finish();
    }
        
    doLongClick(e) {
        const matched = this.frame.raycast(e);
        let found = null;
        
        for (let i=0; i<matched.length; i += 1) {
            let entry = matched[i];
            let name = entry.meta.name;
            let type = entry.meta.type;
            if (type == "planet"  && name != this.name) {
                this.setAnim(name);
                return;
            } else if (type == "orbit"  && ssData.Sol.planets[name]) {
                this.setAnim(name);
                return;
            }
        }
        super.doLongClick(e);
    }
    
    foundBodies(e) {
        var detail = e.detail;
        detail = Object.entries(detail).sort((a, b) => a[1] - b[1]);
        const data = [];
        for (var i in detail) {
            if (detail[i][1] > 0) {
                detail[i][1] = detail[i][1].toFixed(4);                
            }
            data.push({name: detail[i][0], d: detail[i][1]});
        }
        this.setState({nearby: data});
    }
    
    find(name, callback) {
        this.frame.finder.getLocation(name, this.smbData, "smb", callback);
    }

    // Add the fixed sun
    addSun() {
        this.addElement("Sun", Sun());
    }

    // Add the planets
    addPlanets() {

        const names = this.planets;

        // Add each planet in the list.
        for (var xi = 0; xi < names.length; xi += 1) {
            const pname = names[xi];

            const planetData = SolarPlanets.getElements(pname, this.time);
            const qorb       = SolarPlanets.getOrbit(pname, this.time);
            if (qorb == null) {
                Util.log("Unable to process orbit for "+pname);
                continue;
            }
            // Add the orbit line (which is invariant)
            const orbitLine = OrbitPlot(pname, qorb, planetData.color);
            let meta = {...ssData.Sol.planets[pname], orbit: qorb};
            meta.name = pname;
            meta.type = "planet";
            orbitLine.component.meta = {...meta, type:"orbit", otype: "planet"};
            this.addElement(orbitLine.name, orbitLine);

            const planetBody = Planet(pname, this.time, qorb, planetData.diameter, planetData.color, false);
            planetBody.component.meta = {...meta, type:"planet"};
            
            this.addElement(planetBody.name, planetBody);

            // Does this planet have a ring?
            if (planetData.ringAngle) {
                const ringElement = Ring(pname, this.time, qorb, planetData.diameter, planetData.ringColor);
                ringElement.component.meta = {...meta, type:"ring"};
                this.addElement(ringElement.name, ringElement);
            }
        }
    }

    // Add the small bodies point array.
    addSmallBodies() {
        loadMPCData(ssData.Sol.smallBodiesURL, this.time, this.smbCallback.bind(this));
    }
    smbCallback(smbData, epoch) {
        this.smbData = smbData;
        this.smbEpoch = epoch;
        if (!this.frame.times) {
            this.frame.times = {};
        }
        this.frame.times["smb"] = epoch;
        
        this.addElement("SmallBodies", SmallBodies(this.smbData, this.colorLookup, this.time));
    }
        
    raycastResult(results) {
        super.raycastResult(results, "au");
    }
    
    
    
    addExternalOrbit(name, orbit) {
    
        this.addElement(name+"Orbit", OrbitPlot(name, orbit, 0xAAFFAA));
        // If the phase of the orbit has been specified add the body too.
        if (orbit.t0 != null) {
            const userBody = Planet(name+"Body", this.time, orbit, 4000, 0xFFAAFF, true);
            this.addElement(userBody.name, userBody);
        }
    }

}

export default SolarSystem;