import Constants from "../std/Constants.js";
import {Vector3} from  "three";

class Util {
    
    // Log a message to the console.
    static log() {
        var msg = "";
        var div = "";
        for (var i=0; i<arguments.length; i += 1) {
            msg += div + arguments[i];
            div = " ";
        }
        console.log(msg);    
    }
    
    // The determinant of the 3x3 matrix.
    static determinant3(a) {
        const x =  a[0][0]*( (a[1][1]*a[2][2]) - (a[1][2]*a[2][1]) );
        const y = -a[0][1]*( (a[1][0]*a[2][2]) - (a[1][2]*a[2][0]) );
        const z =  a[0][2]*( (a[1][0]*a[2][1]) - (a[1][1]*a[2][0]) );
        return x+y+z;
    }    
    
    // Unit vector in direction of v
    static unit(v) {
        let mag = Util.mag(v);
        if (mag != 0) {
            return Util.times(1/mag, v);
        }
        return v;
    }
    // Return the scalar magnitude of a vector.
    static mag(a) {
        var sum = 0;
        for (var c in a) {
            sum += a[c]*a[c];
        }
        return Math.sqrt(sum);
    }
    
    // The scalar dot product between to vectors
    static dot(a, b) {
        if (a.length !== b.length) {
            throw new Error("Incompatible vectors in dot product");
        }
        var sum = 0;
        for (var i=0; i<a.length; i += 1) {
            sum += a[i]*b[i];
        }
        return sum;
    }
    
    // Multiple two quantities and return the product
    static times(a, b) {
        // We support 
        //    scalar*vector
        //    matrix*vector
        //    matrix*matrix
        
        if (!Array.isArray(b)) {
            throw new Error("Second element in times is scalar, not vector or matrix");
        }
        
        
        if (!Array.isArray(a)) {
            return b.map(x => a*x);
        }
        
        var a0 = a[0];
        // Check that the first element is a matrix
        if (!Array.isArray(a0)) {
            throw new Error("First element in times not scalar or matrix");
        }
                
        if (!Array.isArray(b[0])) {
            
            // Second element is vector so multiply it by matrix
            return [
                Util.dot(a[0], b),
                Util.dot(a[1], b),
                Util.dot(a[2], b)
            ];
        } else {
            // Second element is matrix, so we do matrix multiply here.
            var z = [];
            for (var i=0; i<3; i += 1) {
                z[i] = Util.times(a, b[i]);
            }
            return z;
        }
    }
    
    // Get a row vector from a matrix (given as a vector of columns)
    static rvec(a, n) {
        var res =  [a[0][n], a[1][n], a[2][n]];
        return res;
    }
     
    // The difference of two vectors a-b
    static diff(a,b) {
        if (a.length != b.length) {
            throw new Error("vectors differ in length");
        }
        const res = [];
        for (let i=0; i<a.length; i += 1) {
            res[i] = a[i] - b[i];
        }
        return res;
    }

    // Add two vectors (possibly scaling the second)
    static vadd(a, b, s) {
        if (a.length !== b.length) {
            throw new Error("Incompatible vectors in add");
        }
        if (s == null) {
            s = 1;
        }
        var res = [...a];
        for (var i=0; i < res.length; i += 1) {
            res[i] += s*b[i];
        }
        return res;
    }
    
    // Average of two vectors
    static vavg(a,b) {
        if (a.length !== b.length) {
            throw new Error("Incompatible vectors in average");
        }
        var res = [];
        for (var i=0; i<a.length; i += 1) {
            res[i] = 0.5*(a[i]+b[i]);
        }
        return res;
    }
    
    // Cross product of two vectors.
    static cross(a, b) {
        
        return [
           a[1]*b[2] - a[2]*b[1],
          -a[0]*b[2] + a[2]*b[0],
           a[0]*b[1] - a[1]*b[0]
        ];
    }

    // Rotation matrix for a rotation of the given angle around the specified axis.
    static rot(angle, axis) {
        
        var a,b,c;
        switch (axis) {
            case "x":
                a=1;
                b=2;
                c=0;
                break;
            case "y":
                a=0;
                b=2;
                c=1;
                break;
            case "z":
                a=0;
                b=1;
                c=2;
                break;
            default: throw new Error("Unknown axis");
        }
        
        var m = [[0,0,0],[0,0,0],[0,0,0]];
        
        m[c][c] = 1;
        m[b][b] =  m[a][a] = Math.cos(angle);
        m[a][b] =  Math.sin(angle);
        m[b][a] = -m[a][b];
        return m;
    }
    
    static toRadians = function(a) {
        return a*Math.PI/180;
    }
    
    static toDegrees = function(a) {
        return a*180/Math.PI;
    }

    // Convenience function for printing a matrix
    static printMatrix(a) {
        Util.log("Matrix: ");        
        for (var i=0; i<3; i += 1) {
            Util.log("    "+a[0][i].toFixed(5)+" "+a[1][i].toFixed(5)+" "+a[2][i].toFixed(5));            
        }
    }

    // Given a UTC date return seconds
    // since 2000
    static datetime(str) {
        let flds   = str.split("T");
        let dtflds = flds[0].split("-");
        let tmflds = flds[1].split(":");
        let date = new Date();
        date.setUTCFullYear(dtflds[0]);
        date.setUTCMonth   (dtflds[1]-1); // Stupid!!
        date.setUTCDate    (dtflds[2]);
        
        date.setUTCHours   (tmflds[0]);
        date.setUTCMinutes (tmflds[1]);
        if (tmflds[2]) {
            date.setUTCSeconds(tmflds[2]);
        } else {
            date.setUTCSeconds(0);
        }
        date.setUTCMilliseconds(0);
        return date.getTime()/1000 - Constants.Epoch0;        
    }
    
    // Returns the angle between two vectors.
    // While we can get the cosine of the angle easily
    // from the dot product, the sine is ambiguous, reflecting that we can measure
    // an two angles which sum to 360 degrees. We can say it depends
    // upon the order in which we do the cross-product.  Here
    // the order is defined to be such that the dot
    //     (c X d) dot north 
    // is greater than zero (e.g., that we measure the angle in a prograde 
    // direction were north a vector pointed to a nominal north).  Here c and d are a and b
    // in the desired order.
    static vecAngle(a,b, north=[0,0,1]) {
        let cross = Util.cross(a,b);
        let cdot  = Util.dot(cross,north);
        
        // Magnitude of cross product is |a| |b| |sin(angle)|
        let cmag = Util.mag(cross);
        
        // Magnitude of dot product is |a| |b| cos(angle)
        let dot  = Util.dot(a,b);
        
        // See if we need to negative value for sine.
        if (cdot < 0) {
            cmag = - cmag;
        }
        
        // Now we just return angle.
        let result = Math.atan2(cmag, dot);
        if (result < 0) {
            result += 2*Math.PI;
        }
        
        return result;
    }
    
    /** Project a vector v onto the plane through the origin with the normal vector n.
     *  N is assumed to be normalized 
     */
    static project(v, n) {
        let dot = Util.dot(v, n);
        return Util.vadd(v, n, -dot);        
    }
    
    static pVec(v, nprec) {
        if (nprec == null  || nprec < 0) {
            nprec = 3;
        }
        // First get the maximum scale.
        let max = 0;
        for (let i=0; i<v.length; i += 1) {
            if (Math.abs(v[i]) > max) {
                max = Math.abs(v[i]);
            }
        }
        let scale = 0;
        if (max != 0) {
            scale = Math.floor(Math.log10(max));
        }
        let fac = Math.pow(10, -scale);
        let str = "[ ";
        let divp = " ";
        let divm = "";
        for (let i=0; i<v.length; i += 1) {
            let div = v[i] >= 0? divp: divm;
            str += div + (v[i]*fac).toFixed(nprec);
            divp = "  ";
            divm = " ";
        }
        // Use exactly three digits for the scale (we can handle -99 to 999
        str += " ] x 10^"+scale;
        if (scale >= 0) {
            if (scale < 10) {
                str += "  ";
            } else {
                str += " ";
            }
        } else {
            if (scale >= -9) {
                str += " ";
            }
        }
        return str;
    }
    static showState(r, v, nprec=3, prefix="state") {
        if (Math.abs(r[0]) > 1.e3) {
            r = Util.times(1/Constants.AU, r);
        }
        Util.log(prefix, "r: " + Util.pVec(r, nprec) + "   v: "+Util.pVec(v, nprec));
    }
    
    // Given the rot array, return the standard orbit angles.
    static rotToAngles(rot) {
        let nu    = rot[5]
        let Omega = Math.atan2(rot[2], rot[0])
        let i     = Math.atan2(rot[4], rot[3]/rot[0]);
        return {i: i, Omega: Omega, nu: nu};
    }
    
    // Given a structure with the rotation angles {i,nu,Omega} get the rot array.
    static anglesToRot(str) {
        const o  = str.Omega;
        const co = Math.cos(o);
        const so = Math.sin(o);
                
        const i  = str.i;
        const ci = Math.cos(i);
        const si = Math.sin(i)
                
        const n  = str.nu;
        return [
                
            // Six element vector with to make efficient position computations.
            co,  -so*ci,  // Coefficients for x 
            so,   co*ci,  // Coefficients for y
            si,           // Coefficients for z
            n
        ];            // Save nu so all rotation information stored in rot.    

    }
 
    // Parse a string as a time in ISO, decimal years, or Julian days.
    // Decimal values less than 100000 are assumed to be in years.  Greater
    // (absolute) values are assumed to be Julian days.
    static parseTime(time) {
        
        let aTime = null;
        if (time.indexOf("-") < 1) {  // Allow negative times but any other hyphen is date.
            // Not ISO date so assume numeric
            let numb  = parseFloat(time)
            let scale  = 1;
            let offset = 0;
            time = time.toLowerCase();
            // Keep the order of the following.
            if (time.endsWith("mjd")) {
                scale  = 86400;
                offset = 51544*scale;
                return numb*scale - offset;
            } else if (time.endsWith("jd")) {
                scale = 86400;
                offset = 2451544.5*scale;
                return numb*scale - offset;
            } else if (time.endsWith("d")) {
                scale = 86400;
                return numb*scale;
            } else if (time.endsWith("s")) {
                return numb;
            }
            
            if (numb < 100000) {
                if (time.match(/^\d\d\d\d$/)) {
                    aTime = new Date(time+"-01-01 00:00Z")/1000 - Constants.Epoch0;   
                } else {
                    aTime = (numb-2000)/100 * Constants.CENTURY;
                }
            } else {
                aTime = (numb - Constants.JD0) * 86400;
            }
        } else {
            if (time.indexOf(":") < 0) {
                // Kludge that fixes a particular browser...
                time += " 00:00Z";
            }
            let date = Date.parse(time);
            if (date) {
                aTime = date/1000 - Constants.Epoch0;
            }
        }
        return aTime;
    }
    
    static vec3(arr) {
        return new Vector3(arr[0], arr[1], arr[2]);
    }
    
    // camera: Position of the camera/viewer
    // obj:    Position of the object
    // vel:    Velocity of the object.
    //    Returns unit vectors of standard frame.
    static viewedCoordFrame(camera, obj, objVel) {
        let v = Util.diff(camera, obj);  // Viewing direction (from obj to camera)
        let x = Util.unit(Util.cross(objVel, v));
        let y = Util.unit(objVel);
        let z = Util.cross(x, y);
        return [x,y,z];        
    }
    
    static getDistanceScale(unit) {
        if (unit == null) {
            return null;
        }
        unit = unit.toLowerCase();
        switch (unit) {
            case 'm':  return 1;
            case 'km': return 1000;
            case 'au': return Constants.AU;
            case 'pc': return Constants.PC;
            case 'kpc': return 1000*Constants.PC;
            case 'mpc': return 1000000*Constants.PC;
            default: Util.log("Unknown length unit ("+unit+") returning 1");
                   return 1;
        }
    }
    
    // Fix the range of an angle between -PI to PI
    static fixRange(angle) {
        while (angle > Math.PI) {
            angle -= 2*Math.PI;
        }
        while (angle < -Math.PI) {
            angle += 2*Math.PI;
        }
        return angle;
    }
    
    // Return a string giving the integer value prepended with spaces to extend
    // to length n.
    static prInt(i, n) {
        let s = i+"";
        let d = n - s.length;
        if (d > 0  && d < 30) {
            s = "                              ".substring(0,d) + s;
        }
        return s;
    }
}

export default Util;