class SysInfo {
    // Try to find out if we are on a phone.
    // Using approach from:
    //    https://stackoverflow.com/questions/11381673/detecting-a-mobile-browser 
    // which relies on features relevant to our concerns.
    static isMobile() {
        let match = window.matchMedia || window.msMatchMedia;
        if (match) {
            let mq = match("(pointer:coarse)");
            return mq.matches;
        }
        return false;
    }
}

export default SysInfo;

